/* ////========== SOCIAL STYLES =========//// */

.hl_social {
  .hl_controls {
    margin-bottom: 20px; } }

.hl_social--post-item {
  .card-header {
    padding: 20px 30px;
    .card-header-btns {
      display: flex;
      align-items: center; }
    .social-post-label {
      color: $white;
      display: block;
      padding: 9px 20px;
      margin-right: 10px;
      border-radius: 4px;
      min-width: 100px;
      text-align: center;
      &.--draft {
        background-color: $orange; }
      &.--published {
        background-color: $green; } }
    .avatar {
      @media (min-width: $sm-min) {
        height: 60px;
        .avatar_img {
          min-width: 60px;
          width: 60px;
          height: 60px;
          line-height: 60px;
          font-size: $f18;
          position: relative;

          margin-right: 10px;
          > img {
            max-width: 60px;
            max-height: 60px; } } }
      .avatar_img {
        overflow: visible; }
      .avatar_text {
        h4 {
          margin-bottom: 5px;
          strong {
            font-weight: 500;
            @media (min-width: $sm-min) {
              font-size: $f16; } }
          span {
            @media (min-width: $sm-min) {
              font-size: $f16; } } }
        > * {
          span {
            color: $blue; } } } }
    .icon {
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: $f12;
      min-width: 25px;
      width: 25px;
      height: 25px;
      border-radius: 50%;
      background-color: #000;
      margin-right: 10px;
      color: #fff;
      position: absolute;
      bottom: -5px;
      right: -10px;
      &.--facebook {
        background-color: #2877c1; }
      &.--google-plus {
        background-color: #ff5437; }
      &.--instagram {
        background-color: #f942b4; }
      &.--linkedin {
        background-color: #2877c1; }
      &.--twitter {
        background-color: #65aff5; } } }

  .post-image {
    width: 100%;
    max-width: 100%;
    height: auto; }
  .post-comment {
    padding: 0;
    letter-spacing: 0.5px;
    color: $text;
    border: none;
    background: none;
    &:focus,
    &:active {
      outline: none; } }
  .social-tags {
    .tag {
      display: inline-block;
      margin-right: 10px; } }
  .card-footer {
    padding-left: 30px;
    padding-right: 30px;
    @media (min-width: $lg-min) {
      display: flex;
      min-height: 80px;
      align-items: center;
      > * {
        flex: 1 0 0;
        margin-bottom: 0; } } } }

.hl_social--post-item-details {
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center; }
.hl_social--post-footer-item {
  margin-bottom: 10px;
  h4 {
    font-size: $f16;
    font-weight: 500;
    margin-bottom: 5px; }
  p {
    color: $blue;
    font-size: $f25;
    line-height: 1.2; } }


.hl_social--edit-post-modal {
  .modal-content {
    background-color: $hl-bg; }
  .modal-body {
    padding-top: 40px;
    h3 {
      font-size: $f16;
      margin-bottom: 20px; }
    .row {
      > div {
        @media (max-width: $sm-max) {
          margin-bottom: 40px; } } }
    .card {
      background: #fff; } } }

.hl_edit-post-card {
  .card-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .card-footer-btn {
      .btn {
        &:not(:last-child) {
          margin-right: 5px; } } } } }

.social-post-form {
  position: relative;
  padding-left: 60px;
  margin-bottom: 30px;
  @media (min-width: $sm-min) {
    padding-left: 80px; }
  .avatar {
    position: absolute;
    top: 0;
    left: 0px;
    @media (min-width: $sm-min) {
      height: 60px;
      .avatar_img {
        min-width: 60px;
        width: 60px;
        height: 60px;
        line-height: 60px;
        font-size: $f18;
        position: relative;
        overflow: visible;
        margin-right: 10px;
        > img {
          max-width: 60px;
          max-height: 60px; } } } }
  .dots {
    position: absolute;
    bottom: 0;
    right: 35px;
    color: $text-lt;
    font-size: $f16;
    &:hover,
    &:focus,
    &:active {
      color: $text; } }
  .photo {
    position: absolute;
    bottom: 0;
    right: 10px;
    color: $text-lt;
    font-size: $f16;
    &:hover,
    &:focus,
    &:active {
      color: $text; } } }

.social-post-media {
  background-color: #fcfcfc;
  border-top: solid 2px $hl-bg;
  border-bottom: solid 2px $hl-bg;
  padding: 30px;
  margin-left: -30px;
  margin-right: -30px;
  .progress {
    height: 40px;
    border-radius: 4px;
    position: relative;
    margin-top: 20px;
    .progress-bar {
      font-size: $f14; } } }

.media-preview {
  background: $white;
  padding: 15px;
  border: 1px solid $hl-bg;
  display: flex;
  align-items: flex-start;
  position: relative;
  + .media-preview {
    margin-top: 20px; }
  > img {
    min-width: 150px;
    width: 150px;
    max-width: 150px;
    height: auto;
    margin-right: 15px;
    flex: 1 0 0; }
  .media-preview-text {
    h4 {
      font-size: $f16;
      margin-bottom: 5px; }
    p {
      color: $text-lt; }
    textarea {
      width: 100%;
      color: $text;
      line-height: 1.5;
      border: none;
      padding: 0;
      resize: none;
      &:focus,
      &:active {
        outline: none; }
      &:first-child {
        font-weight: 500;
        color: $text-drk; } }
    .link {
      font-size: $f12;
      color: $text-lt;
      line-height: 1.2; } }
  .remove {
    font-size: 10px;
    position: absolute;
    top: 15px;
    right: 15px;
    color: $text-lt;
    &:focus,
    &:active,
    &:hover {
      color: $text; } } }

.drag-drop {
  color: $text-lt;
  padding: 15px;
  border-radius: 4px;
  border: dashed 1px darken($hl-bg, 12%);
  text-align: center;
  margin-top: 20px; }

.social-post-network {
  padding-top: 30px;
  h4 {
    font-size: $f16;
    margin-bottom: 15px; }
  .bootstrap-select {
    margin-bottom: 30px; } }

.selected-social-network {
  display: flex;
  justify-content: flex-start;
  > * {
    margin-right: 10px;
    margin-bottom: 10px; }
  .avatar {
    position: relative;
    @media (min-width: $sm-min) {
      height: 60px;
      .avatar_img {
        min-width: 60px;
        width: 60px;
        height: 60px;
        line-height: 60px;
        font-size: $f18;
        position: relative;
        margin-right: 10px;
        > img {
          max-width: 60px;
          max-height: 60px; }
        .icon {
          display: flex;
          justify-content: center;
          align-items: center;
          font-size: $f12;
          min-width: 25px;
          width: 25px;
          height: 25px;
          border-radius: 50%;
          background-color: #000;
          margin-right: 10px;
          color: #fff;
          position: absolute;
          bottom: -5px;
          right: -10px;
          &.--facebook {
            background-color: #2877c1; }
          &.--google-plus {
            background-color: #ff5437; }
          &.--instagram {
            background-color: #f942b4; }
          &.--linkedin {
            background-color: #2877c1; }
          &.--twitter {
            background-color: #65aff5; } } } }
    .avatar_img {
      overflow: visible; }
    .remove {
      position: absolute;
      top: 0;
      left: -4px;
      z-index: 2;
      font-size: 8px;
      .icon {
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 8px;
        min-width: 20px;
        width: 20px;
        height: 20px;
        border-radius: 50%;
        background-color: $blue;
        margin-right: 10px;
        color: #fff; } } } }


.hl_edit-post-calendar {
  h4 {
    font-size: $f16;
    margin-bottom: 20px; }
  .social-post-calendar {
    margin-bottom: 20px;
    .fc-toolbar {
      .fc-left {
        h2 {
          font-size: $f16; } } }
    .fc-day-header {
      padding-top: 5px!important;
      padding-bottom: 5px!important;
      font-size: $f10; }
    .fc-basic-view .fc-day-number,
    .fc-basic-view .fc-week-number {
      font-size: $f10!important;
      font-weight: 500!important;
      padding: 10px!important;
      color: $text!important; }
    .fc-day-grid-event {
      color: $white!important;
      border: none!important;
      padding: 5px 10px!important;
      font-size: $f10!important;
      margin-left: 5px!important;
      margin-right: 5px!important; } }
  .hl_edit-post-calendar-select {
    display: flex;
    margin-bottom: 10px;
    > * {
      &:not(:last-child) {
        margin-right: 20px; } } }
  .card-footer {
    padding-top: 30px;
    padding-bottom: 30px;
    .btn {
      float: right; } }
  .scheduled-for {
    span {
      color: $blue; } } }



.hl_social--posts-details {
  .hl_controls {
    min-height: 50px; } }

.hl_social--posts-details-inner {
  .card {
    margin-bottom: 0;
    border-bottom: solid 2px $hl-bg;
    &:last-child {
      border: none; }
    .card-body {
      padding-top: 25px;
      padding-bottom: 25px; } }
  > .hl_social--post-item {
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0; }
  .hl_social--post-item-comment-item {
    border-radius:  0; }
  .hl_social--post-item-add-comment {
    border-radius:  0; } }

.hl_social--post-item-comment-item {
  background-color: #fcfcfc;
  .card-body {
    display: flex;
    align-items: flex-start; }
  .avatar {
    position: relative;
    min-width: 44px;
    margin-right: 20px;
    @media (min-width: $sm-min) {
      min-width: 60px;
      height: 60px;
      margin-right: 25px;
      .avatar_img {
        min-width: 60px;
        width: 60px;
        height: 60px;
        line-height: 60px;
        font-size: $f18;
        position: relative;
        margin-right: 10px;
        > img {
          max-width: 60px;
          max-height: 60px; } } } }
  .social-comment-text {
    h4 {
      font-size: $f16;
      margin-bottom: 5px; }
    .comments-info {
      display: flex;
      color: $text-lt;
      margin-top: 10px;
      p {
        + p {
          margin-top: 0; }
        &:not(:last-child) {
          margin-right: 20px; } } } } }

.view-previous-comments {
  text-align: center;
  background-color: #fcfcfc;
  a {
    display: block;
    padding-top: 20px;
    padding-bottom: 20px;
    font-style: italic;
    font-size: $f15;
    color: $text;
    &:hover,
    &:focus,
    &:active {
      color: $blue; } } }

.social-previous-comments {
  background-color: #fcfcfc;
  .card-body {
    display: flex;
    justify-content: flex-end;
    align-items: flex-start; }
  .previous-comments-wrap {
    width: 100%;
    max-width: 450px; }
  .previous-comments-item {
    display: flex;
    align-items: flex-start;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 30px;
    border-left: 2px solid #f3f3f3;
    h4 {
      font-size: $f16;
      margin-bottom: 5px; }
    p {}
    .avatar {
      margin-right: 20px; }
    .comments-info {
      display: flex;
      color: $text-lt;
      margin-top: 10px;
      p {
        + p {
          margin-top: 0; }
        &:not(:last-child) {
          margin-right: 20px; } } } }
  .previous-add-comments {
    display: flex;
    align-items: flex-start;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 30px;
    .previous-add-comment-form {
      width: 100%;
      flex: 1 0 0; }
    .avatar {
      margin-right: 20px; }
    .previous-add-comment-btn {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      margin-top: 20px;
      > * {
        &:not(:last-child) {
          margin-right: 10px; } } } } }

.view-more-comments {
  text-align: center;
  background-color: #fcfcfc;
  a {
    display: block;
    padding-top: 20px;
    padding-bottom: 20px;
    font-style: italic;
    font-size: $f15; } }

.hl_social--post-item-add-comment {
  .card-body {
    display: flex;
    align-items: flex-start; }
  .avatar {
    position: relative;
    min-width: 44px;
    margin-right: 20px;
    @media (min-width: $sm-min) {
      min-width: 60px;
      height: 60px;
      margin-right: 25px;
      .avatar_img {
        min-width: 60px;
        width: 60px;
        height: 60px;
        line-height: 60px;
        font-size: $f18;
        position: relative;
        margin-right: 10px;
        > img {
          max-width: 60px;
          max-height: 60px; } } } }
  .social-add-comment-form {
    width: 100%;
    flex: 1 0 0; }
  .social-add-comment-btn {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 20px;
    > * {
      &:not(:last-child) {
        margin-right: 10px; } } } }


.hl_social--posts-stats {
  .card {
    margin-bottom: 0;
    border-bottom: solid 2px $hl-bg;
    border-radius: 0;
    &:first-child {
      border-top-left-radius: 4px;
      border-top-right-radius: 4px; }
    &:last-child {
      border: none; }
    .card-body {
      padding-top: 25px;
      padding-bottom: 25px;
      > h4 {
        font-size: $f16;
        margin-bottom: 20px; } } }
  .post-stats-item {
    .post-score-wrap {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center; }
    .post-score-item {
      h4 {
        font-size: $f16;
        font-weight: 500;
        margin-bottom: 5px;
        color: $text; }
      p {
        color: $blue;
        font-size: $f25;
        line-height: 1.2; } } }
  .post-stats-chart-wrap {
    display: flex;
    flex-wrap: wrap;
    align-items: center; }
  .post-stats-chart {
    width: 100%;
    min-width: 200px;
    max-width: 200px;
    height: 200px;
    margin-right: 40px;
    .highcharts-credits {
      display: none!important;
      opacity: 0!important; } } }
.chart-scores {
  .chart-score-item {
    margin-bottom: 5px;
    display: flex;
    align-items: center;
    &:before {
      content: "";
      display: block;
      width: 10px;
      height: 10px;
      border-radius: 2px;
      background-color: $text;
      margin-right: 10px;
      position: relative;
      top: -1px; }
    strong {
      font-size: $f16;
      display: inline-block;
      margin-right: 5px; } }
  &.--people-reached-chart {
    .chart-score-item {
      &:nth-child(1) {
        &:before {
          background-color: #ffbc00; } }
      &:nth-child(2) {
        &:before {
          background-color: #e6edf2; } } } }
  &.--story-teller-gender-chart {
    .chart-score-item {
      &:nth-child(1) {
        &:before {
          background-color: #188bf6; } }
      &:nth-child(2) {
        &:before {
          background-color: #ff72f4; } } } }
  &.--sentiment-chart {
    .chart-score-item {
      &:nth-child(1) {
        &:before {
          background-color: #37ca37; } }
      &:nth-child(2) {
        &:before {
          background-color: #e93d3d; } } } } }

.no-content {
  padding-top: 50px;
  padding-bottom: 50px;
  text-align: center;
  font-style: italic;
  color: $text-lt;
  p {
    max-width: 350px;
    margin: 0 auto; } }


//Calendar
.fc-toolbar {
  .fc-left {
    h2 {
      font-size: $f20; } }
  .fc-right {
    button {
      background: #fff;
      box-shadow: none;
      border: 1px solid darken($border, 5%);
      color: $text-drk;
      .fc-icon {
        &:after {
          font-size: $f20;
          color: $text-drk; } } } } }

.fc td,
.fc th {
  background-color: $white!important; }

.fc-unthemed td.fc-today {
  background: lighten($blue, 43%)!important; }


.fc-day-header {
  padding-top: 10px!important;
  padding-bottom: 10px!important;
  font-size: $f16;
  font-weight: 500; }

.fc-basic-view .fc-day-number,
.fc-basic-view .fc-week-number {
  font-size: $f16!important;
  font-weight: 500!important;
  padding: 10px!important;
  color: $text!important; }

.fc-day-grid-event {
  color: $white!important;
  border: none!important;
  padding: 7px 15px!important;
  font-size: $f14!important;
  margin-left: 10px!important;
  margin-right: 10px!important; }

.fc-unthemed .fc-content,
.fc-unthemed .fc-divider,
.fc-unthemed .fc-list-heading td,
.fc-unthemed .fc-list-view,
.fc-unthemed .fc-popover,
.fc-unthemed .fc-row,
.fc-unthemed tbody,
.fc-unthemed td,
.fc-unthemed th,
.fc-unthemed thead {
  border-color: darken($hl-bg, 5%)!important; }

.calendar-popover {
  border: none;
  box-shadow: 0 8px 16px 5px rgba(0, 0, 0, 0.1);
  width: 360px;
  max-width: 360px;
  border-radius: 4px;
  background: $white;
  .popover-body {
    padding: 30px; }
  .calendar-popover-header {
    padding-left: 30px;
    padding-right: 30px;
    padding-bottom: 20px;
    margin-left: -30px;
    margin-right: -30px;
    margin-bottom: 20px;
    border-bottom: 2px solid $border;
    position: relative;
    h4 {
      font-size: $f16;
      margin-bottom: 5px; }
    .date-time {
      .time {
        color: $blue; } }
    .icon-close {
      position: absolute;
      top: -10px;
      right: 20px;
      cursor: pointer; } }
  .calendar-label {
    border-radius: 4px;
    color: $white;
    padding: 7px 15px;
    background: $text-lt;
    margin-bottom: 15px;
    &.--blue {
      background-color: $blue; }
    &.--yellow {
      background-color: $yellow; }
    &.--green {
      background-color: $green; }
    &.--red {
      background-color: $red; }
    &.--orange {
      background-color: $orange; }
    &.--purple {
      background-color: $purple; }
    &.--teal {
      background-color: $teal; }
    &.--pink {
      background-color: $pink; }
    &.--gray {
      background-color: $text; }
    &.--gray-drk {
      background-color: $text-drk; }
    &.--gray-lt {
      background-color: $text-lt; } }
  .calendar-popover-body {
    margin-bottom: 20px;
    > img {
      display: block;
      max-width: 100%;
      height: auto;
      margin-bottom: 10px; } }
  .calendar-popover-footer {
    display: flex;
    justify-content: flex-end;
    .btn {
      padding: 8px 15px;
      &:not(:last-child) {
        margin-right: 10px; } } } }
