//Calendar Wrap
.hl_calendar--wrap {
  overflow-y: auto; }

//Calendar Selection
.hl_calendar--selection {
  min-width: 800px;
  margin-left: 0;
  margin-right: 0;
  @media (max-width: 950px) {
    padding-right: 20px; }
  > .hl_calendar--set {
    padding-left: 10px;
    padding-right: 10px;
    @media (max-width: 950px) {
      padding-left: 5px;
      padding-right: 5px; } } }

//Calendar Set
.hl_calendar--set {
  text-align: center;
  h3 {
    font-size: $f12;
    font-weight: 500;
    text-transform: uppercase;
    margin-bottom: 15px; } }

//Calendar Hours
.hl_calendar--hours {
  list-style: none;
  padding: 0;
  margin: 0;
  li {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 38px;
    line-height: 1;
    border-radius: 0.3125rem;
    border: solid 1px darken($hl-grey, 10%);
    margin-bottom: 10px;
    transition: $hover;
    font-size: $f13;
    color: $hl-text-drk;
    padding: 5px;
    cursor: pointer;
    transtion: all .2s ease-in-out;
    &:hover {
      background-color: $green;
      border-color: $green;
      color: $white; }
    @media (max-width: 950px) {
      font-size: $f12; }
    &.booked {
      background-color: $green;
      border-color: $green;
      color: $white; }
    &.unavailable {
      background-color: $red;
      border-color: $red;
      color: $white; }
    &.closed {
      background-color: $hl-grey;
      color: rgba($text-drk, 0.4);
      border-color: $hl-grey;
      cursor: no-drop; } } }

.popover {
  border: none;
  bow-shadow: none;
  .arrow {
    display: none; }
  .popover-body {
    padding: 0;
    bow-shadow: none; }
  .calendar-popover {
    text-align: center;
    padding: 30px;
    min-width: 320px;
    background: $white;
    box-shadow: 0 8px 24px 0 rgba(0, 0, 0, 0.1);
    position: relative;
    &--header {
      padding: 50px 0;
      margin-top: -30px;
      margin-left: -30px;
      margin-right: -30px;
      margin-bottom: 30px;
      color: #fff;
      background-image: linear-gradient(to right, #2fcdff, #159fff);
      .avatar {
        height: 72px;
        &_img {
          min-width: 72px;
          width: 72px;
          height: 72px;
          line-height: 72px;
          font-size: $f20;
          font-weight: 500;
          > img {
            max-width: 72px;
            max-height: 72px; } }
        @include center-align();
        margin-bottom: 20px; }
      h4 {
        font-size: $f18;
        font-weight: 500;
        color: $white;
        margin-bottom: 5px; }
      p {
        line-height: 1;
        font-size: $f14;
        text-align: center; } }
    .popover-close {
      position: absolute;
      top: 10px;
      right: 10px;
      z-index: 2;
      color: #fff;
      font-size: $f12; }
    .popover-appointment {
      display: flex;
      justify-content: space-between;
      margin-bottom: 30px;
      .appointment-item {
        flex: 1 0 0;
        width: 50%;
        &:last-child {
          border-left: 1px solid $border; } }
      h4 {
        font-size: $f12;
        font-weight: normal;
        color: $text;
        margin-bottom: 0; }
      p {
        font-size: $f14;
        color: $text-drk; }
      a {
        font-size: $f14; } }
    .popover-btns {
      margin-bottom: 25px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: -55px;
      .popover-chat {
        display: block;
        width: 48px;
        height: 48px;
        box-shadow: 0 4px 16px 0 rgba(0, 0, 0, 0.2);
        background-color: $white;
        border-radius: 50%;
        margin: 0 20px;
        text-align: center;
        line-height: 48px;
        font-size: 16px;
        color: $blue;
        transition: $hover;
        &:hover,
        &:focus,
        &:active {
          box-shadow: 0 4px 16px 1px rgba(0, 0, 0, 0.25); } }
      .popover-message {
        display: block;
        width: 48px;
        height: 48px;
        box-shadow: 0 4px 16px 0 rgba(0, 0, 0, 0.2);
        background-color: $white;
        border-radius: 50%;
        margin: 0 20px;
        text-align: center;
        line-height: 48px;
        font-size: 16px;
        color: $green;
        transition: $hover;
        &:hover,
        &:focus,
        &:active {
          box-shadow: 0 4px 16px 1px rgba(0, 0, 0, 0.25); } } }
    .popover-infos {
      text-align: left;
      margin-bottom: 30px;
      p {
        font-size: $f14;
        color: $text;
        i {
          margin-right: 10px;
          position: relative;
          top: 4px; }
        + p {
          margin-top: 10px; } }
      .icon-users {
        display: inline-block;
        width: 14px;
        height: 16px;
        background: url('../img/icon-user.svg')no-repeat center;
        background-size: 14px 16px; }
      .icon-gender {
        display: inline-block;
        width: 16px;
        height: 16px;
        background: url('../img/icon-gender.svg')no-repeat center;
        background-size: 16px; }
      .icon-phone {
        display: inline-block;
        width: 16px;
        height: 16px;
        background: url('../img/icon-phone.svg')no-repeat center;
        background-size: 16px; }
      .icon-email {
        display: inline-block;
        width: 16px;
        height: 16px;
        background: url('../img/icon-email.svg')no-repeat center;
        background-size: 16px; } } } }
