/* ////========== MARKETING STYLES =========//// */

.hl_marketing {
  //header
  &--header {
    border-bottom: 2px solid $hl-grey;
    margin-top: 20px;
    margin-bottom: 35px;
    @media (min-width: $md-min) {
      margin-top: 110px; }
    &-inner {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 20px;
      h2 {
        font-size: $f24;
        .icon {
          display: inline-block;
          font-size: $f14;
          color: $text-lt;
          margin-left: 10px; } }
      .btn {
        .icon {
          color: $green; } } }
    &-btns {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      > * {
        &:not(:last-child) {
          margin-right: 10px; } } } }

  //nav
  &--nav {
    list-style: none;
    margin: 0;
    padding: 0;
    @media (max-width: $sm-max) {
      padding-bottom: 10px; }
    @media (min-width: $md-min) {
      transform: translateY(2px); }
    li {
      @media (min-width: $md-min) {
        display: inline-block;
        &:not(last-child) {
          padding-right: 15px; } }
      @media (min-width: $lg-min) {
        &:not(last-child) {
          padding-right: 30px; } }
      a {
        font-size: $f14;
        color: $text;
        @media (min-width: $md-min) {
          display: block;
          padding-bottom: 10px;
          border-bottom: 3px solid transparent; }
        &:hover,
        &:focus,
        &:active {
          color: $blue; } }
      &.active {
        a {
          color: $blue;
          font-weight: 500;
          border-color: $blue; } } } }
  //heading
  &--heading {
    margin-bottom: 40px;
    h3 {
      font-size: $f20;
      margin-bottom: 5px; }
    &.--info {
      background-color: darken(#f3f8fb, 4%);
      padding: 20px;
      display: flex;
      border-radius: 4px;
      .icon {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        color: $blue;
        border: 2px solid $blue;
        margin-right: 20px;
        font-size: $f16; }
      h3 {
        font-size: $f18;
        margin-bottom: 0; } } } }



//Customer Acquisitions
.hl_customer-acquisition {
  .card {
    .card-header {
      padding: 40px 30px 0px 30px;
      border-color: $white;
      @media (min-width: $sm-min) {
        padding: 40px 40px 0px 40px; }
      h2 {
        color: $text; } }
    .card-body {
      @media (min-width: $sm-min) {
        padding: 25px 50px 40px 50px; }
      h3 {
        font-size: 54px;
        font-weight: 300;
        line-height: 1;
        margin-bottom: 0;
        vertical-align: top;
        .icon {
          display: inline-block;
          font-size: 18px;
          vertical-align: top;
          position: relative;
          top: 4px; } } }
    &:nth-child(1) {
      h3 {
        .icon {
          color: $blue; } } }
    &:nth-child(2) {
      h3 {
        .icon {
          color: $yellow; } } }
    &:nth-child(3) {
      h3 {
        .icon {
          color: $green; } } } } }


.hl_customer-acquisition--filter {
  h3 {
    font-size: 18px; }
  .card {
    //background-color: #E7F3FE
    h3 {
      font-size: 20px;
      color: $text-drk; }
    .card-header {
      padding: 20px 25px!important;
      //border: none!important
      border-bottom: 2px solid $border;
      ul {
        list-style: none;
        display: flex;
        align-items: center;
        margin-bottom: 0;
        padding: 0;
        li {
          a {
            font-size: 12px; }
          &:not(:last-child) {
            margin-right: 10px; } } } }
    .card-body {
      padding: 20px 25px 0 25px!important; }
    .form-group {
      label {
        color: $text-drk;
        font-size: 14px;
        font-weight: 500; } }
    .form-control {
      //background-color: #fff
      height: 40px; }
    .bootstrap-select {
      > .btn.dropdown-toggle {
        height: 40px;
        //background-color: #fff
        .filter-option {
          top: 0; } } }
    .status-item,
    .state-item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 10px;
      p {
        font-size: 13px; }
      .option {
        position: relative;
        top: -16px;
        right: -10px; } } } }

.hl_customer-acquisition--table {
  .table {
    tbody {
      tr {
        td {
          &:first-child {
            font-weight: 500;
            color: $blue; }
          .active {
            display: inline-flex;
            justify-content: center;
            align-items: center;
            width: 26px;
            height: 26px;
            border-radius: 50%;
            color: $green;
            border: 1px solid $green;
            font-weight: normal;
            font-size: 14px;
            margin-right: 10px;
            line-height: 1; } } } } } }



//
.hl_create-campaign {
  margin-bottom: 40px;
  .card-collapsing {
    margin-bottom: 10px;
    > a {
      &.collapsed {
        .card-header {
          h2 {
            .icon {
              transform: none; } } } } }
    .card-header {
      transition: $hover;
      h2 {
        transition: $hover;
        .icon {
          display: inline-block;
          font-size: $f12;
          margin-right: 10px;
          transform: rotate(90deg); } }
      &:hover {
        h2 {
          color: $blue; } } }
    .card-footer {
      padding-left: 30px;
      padding-right: 30px;
      .btn {
        margin-right: 10px; } } }
  .add-new-event {
    padding-top: 40px;
    padding-bottom: 40px;
    text-align: center;
    h4 {
      font-size: 18px;
      margin-bottom: 20px; }
    .btn {
      .icon {
        margin-right: 10px; } } }
  .new-event-day-item {
    position: relative;
    @media (min-width:$md-min) {
      display: flex;
      align-items: flex-start; }
    + .new-event-day-item {
      margin-top: 30px; }
    .calendar-day {
      @media (max-width:$sm-max) {
        margin-bottom: 20px; }
      @media (min-width:$md-min) {
        &:before {
          content: "";
          display: block;
          width: 1px;
          background-color: darken($border, 5%);
          position: absolute;
          margin: auto;
          top: 0;
          left: 32px;
          bottom: -30px;
          z-index: 1; } }
      &-inner {
        border: 1px solid darken($border, 5%);
        background-color: #F6F9FB;
        border-radius: 8px;
        margin-right: 20px;
        width: 65px;
        min-width: 65px;
        padding-top: 10px;
        padding-bottom: 10px;
        text-align: center;
        position: relative;
        z-index: 2; }
      h5 {
        font-size: 12px;
        color: $text; }
      p {
        color: $text-drk;
        font-size: 20px;
        font-weight: bold;
        line-height: 1.2; } }
    .event-card {
      flex: 1 0 0; }
    .event-card--top {
      background-color: darken($border, 3%);
      padding: 30px;
      border-top-left-radius: .3125rem;
      border-top-right-radius: .3125rem;
      @media (min-width:$md-min) {
        display: flex;
        //justify-content: space-between
        align-items: flex-start; }
      > * {
        &:not(:last-child) {
          margin-right: 30px;
          @media (max-width:$sm-max) {
            margin-bottom: 20px; } } }
      .event-card-data {
        flex: 1 0 0;
        h5 {
          margin-bottom: 5px;
          &.--blue {
            color: $blue; }
          &.--yellow {
            color: $yellow; }
          &.--red {
            color: $red; }
          &.--grey {
            color: $text-lt; }
          &--green {
            color: $green; } }
        p {
          line-height: 1.2;
          &.--blue {
            color: $blue; }
          &.--yellow {
            color: $yellow; }
          &.--red {
            color: $red; }
          &.--grey {
            color: $text-lt; }
          &--green {
            color: $green; } } }
      .event-card--title {
        display: flex;
        align-items: flex-start;
        flex: 3 0 0;
        .icon {
          font-size: 20px;
          margin-right: 10px;
          color: $blue; }
        h5 {
          font-size: 18px;
          margin-bottom: 5px; } } }
    .event-card--bottom {
      background-color: $border;
      padding: 20px 30px;
      border-bottom-left-radius: .3125rem;
      border-bottom-right-radius: .3125rem;
      display: flex;
      justify-content: space-between;
      .actions {
        display: flex;
        align-items: center;
        list-style: none;
        padding: 0;
        margin: 0;
        > * {
          &:not(:last-child) {
            margin-right: 20px; } }
        a {
          font-size: 13px; } }
      p {
        font-size: 13px;
        span {
          display: inline-block;
          background: #fff;
          padding-left: 10px;
          padding-right: 10px;
          min-width: 50px;
          margin-left: 5px;
          margin-right: 5px;
          color: $text-drk; } } }
    .more-info {
      border: 1px solid darken($border, 3%);
      background-color: lighten($border, 3%);
      padding: 20px;
      width: 100%;
      @media (min-width:$md-min) {
        display: flex;
        //justify-content: space-between
        align-items: flex-start; }
      > * {
        &:not(:last-child) {
          margin-right: 30px;
          @media (max-width:$sm-max) {
            margin-bottom: 20px; } } }
      .more-info-data {
        flex: 1 0 0;
        h5 {
          margin-bottom: 5px;
          &.--blue {
            color: $blue; }
          &.--yellow {
            color: $yellow; }
          &.--red {
            color: $red; }
          &.--grey {
            color: $text-lt; }
          &--green {
            color: $green; } }
        p {
          line-height: 1.2;
          font-size: 14px;
          &.--blue {
            color: $blue; }
          &.--yellow {
            color: $yellow; }
          &.--red {
            color: $red; }
          &.--grey {
            color: $text-lt; }
          &--green {
            color: $green; } } } }
    .add-new-event-btn {
      .icon {
        display: inline-flex;
        justify-content: center;
        align-items: center;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        background-color: $green;
        color: #fff;
        margin-left: 17px;
        position: relative;
        z-index: 2;
        margin-right: 40px; }
      span {
        font-size: 16px;
        font-weight: 500;
        color: $green; } } } }

.card-campaign-status {
  .card-body {
    padding: 0;
    display: flex; }
  .campaign-status {
    display: flex;
    width: 100%;
    > div {
      flex: 1 0 0;
      &.campaign-status-left {
        border-right: 2px solid $border;
        flex: 2 0 0;
        display: flex;
        flex-direction: column;
        > div {
          padding: 40px;
          &.campaign-status-top {
            border-bottom: 2px solid $border; } } }
      &.campaign-status-right {
        padding: 40px; } } }
  .stat-item {
    h3 {
      font-size: 50px;
      color: $blue;
      margin-bottom: 5px;
      line-height: 1; }
    p {
      font-size: 16px; } }
  .item {
    h4 {
      font-size: 18px;
      margin-bottom: 0;
      font-weight: 500;
      span {
        color: $text;
        font-weight: normal;
        font-size: 16px; } }
    p {
      font-size: 13px; }
    &:not(:last-child) {
      margin-bottom: 25px; } } }

.card-email-performance {
  .card-body {
    padding: 0;
    display: flex; }
  .email-performance {
    display: flex;
    width: 100%;
    > div {
      flex: 1 0 0;
      &.email-performance-left {
        border-right: 2px solid $border;
        flex: 2 0 0;
        display: flex;
        flex-direction: column;
        > div {
          padding: 40px;
          height: 100%;
          &.email-performance-top {
            border-bottom: 2px solid $border; }
          &.email-performance-bottom {
            display: flex;
            align-items: center;
            justify-content: center;
            .btn {
              @include center-align();
              max-width: 200px; } } } }
      &.email-performance-right {
        padding: 40px; } } }
  .stat-item {
    h3 {
      font-size: 50px;
      margin-bottom: 5px;
      line-height: 1;
      .icon {
        font-size: 20px;
        color: $red; } }
    p {
      font-size: 16px; } }
  .item {
    h4 {
      font-size: 18px;
      margin-bottom: 0;
      font-weight: 500;
      span {
        color: $text;
        font-weight: normal;
        font-size: 16px; } }
    p {
      font-size: 13px; }
    &:not(:last-child) {
      margin-bottom: 25px; } } }





//
.hl_campaign-configuration {
  .alert {
    margin-bottom: 40px; }
  h3 {
    font-size: 20px;
    margin-bottom: 5px; }
  hr {
    max-width: 500px;
    margin-left: 0;
    margin-top: 10px;
    margin-bottom: 40px; }
  .form-group {
    @media (min-width:$md-min) {
      display: flex;
      align-items: flex-start;
      margin-bottom: 40px; }
    > label {
      font-size: 14px;
      color: $text-drk;
      @media (min-width:$md-min) {
        min-width: 150px; } }
    .bootstrap-select {
      min-width: 150px;
      > .btn.dropdown-toggle {
        height: 40px;
        .filter-option {
          top: 0; } } }
    p {
      font-size: 12px;
      margin-top: 10px;
      + p {
        margin-top: 0; } } }
  .week-select {
    display: flex;
    align-items: flex-start;
    .week-select-day {
      input {
        display: none;
        visibility: hidden;
        opacity: 0;
        &:checked {
          + label {
            background-color: $blue;
            color: #fff; } } }
      label {
        background-color: darken(#f7fafc, 1%);
        //border: 1px solid darken(#f7fafc, 4%)
        color: $text-drk;
        margin-right: 4px;
        padding: 3px 10px;
        border-radius: 3px;
        cursor: pointer; } } } }


//
.hl_add-event-modal {
  .modal-body {
    .row {
      > div {
        margin-top: 20px;
        margin-bottom: 20px;
        a {
          text-align: center;
          .icon {
            display: block;
            margin-bottom: 10px;
            font-size: 24px;
            color: $green; }
          h4 {
            font-size: 18px;
            transition: $hover; }
          &:hover {
            h4 {
              color: $green; } } } } } } }


//
.hl_campaign-activity {
  .card-campaign-activity {
    .card-header {
      flex-direction: column;
      padding: 0;
      > * {
        width: 100%;
        padding: 20px 30px;
        background-color: rgba($border, 0.3); } }
    .card-header-top {
      border-bottom: 2px solid $border;
      display: flex;
      align-items: center;
      > * {
        margin-right: 20px; }
      .campaign-activity-filter {
        font-size: $f18;
        color: $text;
        &:hover,
        &:focus,
        &:active {
          color: $blue; }
        &:after {
          display: none; } }
      > p {
        font-style: italic; }
      .dropdown-menu {
        padding: 20px;
        font-size: 14px;
        min-width: 12rem;
        h4 {
          font-size: 16px;
          margin-left: -20px;
          margin-right: -20px;
          margin-bottom: 15px;
          padding-left: 20px;
          padding-right: 20px;
          padding-bottom: 15px;
          border-bottom: 2px solid $border; }
        .filter-group {
          margin-left: -20px;
          margin-right: -20px;
          padding-left: 20px;
          padding-right: 20px;
          &:not(:last-child) {
            margin-bottom: 15px;
            padding-bottom: 15px;
            border-bottom: 1px solid $border; }
          h5 {
            font-size: 14px; }
          .select-control {
            position: relative;
            .icon {
              font-size: $f12;
              position: absolute;
              top: 15px;
              right: 15px; } }
          select.form-control {
            height: 40px;
            padding: 0 15px;
            cursor: pointer; }
          .filter-item {
            font-size: $f10;
            display: inline-block;
            background: rgba($blue, 0.1);
            padding: 4px 15px;
            border-radius: 5px;
            padding-right: 20px;
            position: relative;
            .icon {
              font-size: $f8;
              color: $text;
              display: inline-block;
              position: absolute;
              top: 10px;
              right: 10px;
              &:hover,
              &:focus,
              &:active {
                color: $blue; } } } } } }

    .card-header-bottom {
      display: flex;
      align-items: center;
      > * {
        margin-right: 20px; }
      .filter-item {
        font-size: $f12;
        display: inline-block;
        background: rgba($blue, 0.1);
        padding: 4px 15px;
        border-radius: 5px;
        .icon {
          font-size: $f10;
          color: $text;
          display: inline-block;
          margin-left: 10px;
          &:hover,
          &:focus,
          &:active {
            color: $blue; } } } }
    .card-body {
      padding-left: 0;
      padding-right: 0; }
    .table {
      tbody {
        td {
          a {
            color: $blue;
            h4 {
              color: $blue;
              margin-bottom: 0; } }
          h4 {
            font-size: 14px; } } } } } }

