/* ////========== AGENCY STYLES =========//// */

.hl_agency {
  .hl_controls {
    margin-top: 10px;
    margin-bottom: 30px;
    h3 {
      font-size: $f24; } } }

.hl_header {
  &.--agency {
    border-bottom: 3px solid $green; } }

.hl_agency-dashboard--cards {
  align-items: stretch;
  width: 100%;
  > .card {
    border-radius: 4px!important;
    &:nth-child(1) {
      background-image: linear-gradient(to right, lighten($blue, 10%), $blue); }
    &:nth-child(2) {
      background-image: linear-gradient(to right, lighten($yellow, 10%), $yellow); }
    &:nth-child(3) {
      background-image: linear-gradient(to right, lighten($green, 10%), $green); } }
  .card {
    height: 100%;
    width: 100%;
    color: $white;
    position: relative;
    overflow: hidden;
    &-body {
      display: flex;
      flex-direction: column;
      justify-content: flex-end; }
    .icon {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 36px;
      height: 36px;
      border-radius: 50%;
      background-color: $white;
      margin-bottom: 15px; }
    h4 {
      color: $white;
      margin-bottom: 5px;
      font-size: $f14; }
    p {
      font-size: $f36;
      line-height: 1; }
    .card-bg {
      position: absolute;
      right: -10px;
      bottom: -20px; } } }


.hl_agency-dashboard--table {
  .table {
    tbody {
      tr {
        td {
          &:nth-child(2) {
            @media (min-width: $lg-min) {
              width: 250px; } }
          &:last-child {
            @media (min-width: $lg-min) {
              width: 80px; } } } } }
    .table_a-progress {
      display: flex;
      align-items: center;
      p {
        color: $text-drk;
        margin-right: 15px;
        text-align: right; }
      .progress {
        flex: 1 0 0;
        width: 200px;
        max-width: 200px;
        @media (min-width: $xl-min) {
          width: 250px;
          max-width: 250px; } } }
    .table_a-product {
      display: flex;
      align-items: center;
      .icon {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 36px;
        height: 36px;
        border-radius: 50%;
        margin-right: 15px;
        background-color: $text;
        &.--green {
          background-color: $green; }
        &.--yellow {
          background-color: $yellow; }
        &.--blue {
          background-color: $blue; }
        &.--pink {
          background-color: $pink; } }
      p {
        line-height: 1; } } } }


//Location
.hl_location--group {
  position: relative;
  overflow: hidden;
  transition: $basic;
  min-height: 640px;
  &.--open {
    @media(min-width: $md-min) {
      padding-right: 300px; } } }

//RLocation Filter
.hl_location--filter {
  @media(max-width: $sm-max) {
    border-top: 1px solid $hl-bg;
    height: 0;
    visibility: hidden;
    margin-bottom: 20px; }
  @media(min-width: $md-min) {
    transition: $basic;
    opacity: 0;
    width: 300px;
    padding: 20px 0 0 30px;
    position: absolute;
    top: 0;
    right: 0;
    transform: translateX(300px); }
  &.--open {
    @media(max-width: $sm-max) {
      height: auto;
      visibility: visible;
      position: relative; }
    @media(min-width: $md-min) {
      transform: translateX(0);
      opacity: 1; } }
  .form-group {
    margin-bottom: 20px; }
  .bootstrap-select {
    > .btn.dropdown-toggle {
      background-color: rgba($hl-blue, 0.1);
      .filter-option {
        color: $text-drk;
        font-weight: 400;
        .icon {
          font-size: $f8;
          color: $hl-text-lt;
          margin-right: 5px; } }
      &.bs-placeholder {
        background: #fff;
        .filter-option {
          color: $text; } } }
    .dropdown-menu {
      .icon {
        font-size: $f8;
        color: $hl-text-lt;
        margin-right: 5px; } } } }

.hl_location--item {
  &-inner {
    padding-top: 30px;
    padding-left: 30px;
    display: flex;
    align-items: flex-start; }
  .card-body {
    padding: 0 30px 30px 0;
    flex: 1 0 0; }
  .hl_icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 36px;
    height: 36px;
    border-radius: 50%;
    background-color: $text;
    &.--green {
      background-color: $green; }
    &.--yellow {
      background-color: $yellow; }
    &.--blue {
      background-color: $blue; }
    &.--pink {
      background-color: $pink; } } }

.hl_location--logo {
  width: 60px;
  margin-right: 20px;
  > img {
    max-width: 100%;
    height: auto; } }

.hl_location--header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 10px;
  > * {
    margin-bottom: 10px; }
  h3 {
    font-size: $f16;
    font-weight: 500;
    color: $blue;
    margin-bottom: 5px;
    .label {
      display: inline-block;
      padding: 3px 8px;
      border-radius: 3px;
      font-size: $f11;
      font-weight: 500;
      margin-left: 5px;
      &.--green {
        background-color: lighten($green, 40%);
        color: $green; }
      &.--blue {
        background-color: lighten($blue, 40%);
        color: $blue; } } }
  .hl_location--header-right {
    display: flex;
    .hl_icon {
      &:not(:last-child) {
        margin-right: 15px; } } } }

.hl_location--info-list {
  margin-bottom: 0;
  li {
    &:not(:last-child) {
      margin-bottom: 5px; }
    .icon {
      margin-right: 5px; } } }

.hl_location--footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding: 15px 30px 5px 30px;
  > * {
    margin-bottom: 10px; }
  a {
    .icon {
      font-size: $f10; } }
  .list-inline {
    margin-bottom: 0;
    li {
      &:not(:last-child) {
        margin-right: 20px; }
      .icon {
        margin-right: 5px;
        position: relative;
        top: 2px;
        &.icon-a-refresh {
          margin-left: 5px; } } } } }

.icon-a-pin {
  display: inline-block;
  width: 14px;
  height: 14px;
  background: url('../img/icon-a-pin.svg')no-repeat center;
  background-size: 12.25px 14px; }
.icon-a-phone {
  display: inline-block;
  width: 14px;
  height: 14px;
  background: url('../img/icon-a-phone.svg')no-repeat center;
  background-size: 14px 14px; }
.icon-a-report {
  display: inline-block;
  width: 16px;
  height: 16px;
  background: url('../img/icon-a-report.svg')no-repeat center;
  background-size: 16px 16px; }
.icon-a-mail {
  display: inline-block;
  width: 16px;
  height: 16px;
  background: url('../img/icon-a-mail.svg')no-repeat center;
  background-size: 16px 12px; }
.icon-a-refresh {
  display: inline-block;
  width: 12px;
  height: 12px;
  background: url('../img/icon-a-refresh.svg')no-repeat center;
  background-size: 12px 12px; }



//Map
.hl_agency-location--map {
  padding: 0;
  position: relative; }

.the-location-map {
  width: 100%;
  min-height: 500px;
  height: calc(100vh - 82px); }

.hl_location--search {
  padding-left: 15px;
  padding-right: 15px;
  position: absolute;
  margin: auto;
  top: 30px;
  left: 0;
  right: 0;
  z-index: 3;
  max-width: 900px;
  @include center-align();
  .icon {
    position: absolute;
    top: 22px;
    left: 35px;
    color: #b0b8bc;
    font-size: $f16; }
  .form-control {
    padding-left: 50px;
    padding-right: 150px;
    background: $white;
    border-radius: 8px;
    background-color: $white;
    box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.1);
    height: 60px;
    font-size: $f18;
    transition: none;
    color: $text-drk;
    &.active {
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0; } }
  .dropdown {
    .dropdown-menu {
      list-style: none;
      width: 100%;
      margin: auto!important;
      top: 0px;
      border: none;
      border-top: 1px solid #e4e7ea;
      right: 0;
      border-top-left-radius: 0;
      border-top-right-radius: 0;
      box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.1);
      .dropdown-item {
        font-size: $f14; } } }
  .add-manually {
    font-size: $f12;
    position: absolute;
    top: 22px;
    right: 35px; } }

.hl_location--search-results {
  position: absolute;
  margin: auto;
  top: 130px;
  right: 15px;
  z-index: 2;
  width: 100%;
  max-width: 445px;
  padding: 30px;
  border-radius: 4px;
  background-color: $white;
  box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.1); }
.search-results-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 30px;
  h4 {
    font-size: $f18;
    margin-bottom: 0; }
  .hide {
    font-size: $f12; } }

.search-result-item {
  display: block;
  position: relative;
  padding-left: 50px;
  margin-bottom: 20px;
  &:hover {
    .icon-arrow-right-1 {
      transform: translateY(-50%) translateX(5px); } }
  .icon-pin-filled {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 36px;
    height: 36px;
    border-radius: 50%;
    background: $text-drk;
    color: $white;
    position: absolute;
    top: -2px;
    left: 0;
    &.--blue {
      background-color: $blue; }
    &.--yellow {
      background-color: $yellow; }
    &.--green {
      background-color: $green; }
    &.--red {
      background-color: $red; }
    &.--orange {
      background-color: $orange; }
    &.--purple {
      background-color: $purple; }
    &.--teal {
      background-color: $teal; }
    &.--pink {
      background-color: $pink; } }
  h5 {
    font-size: $f12;
    font-weight: 500;
    margin-bottom: 5px; }
  p {
    font-size: $f12;
    color: $text; }
  .icon-arrow-right-1 {
    font-size: $f8;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    color: $blue;
    transition: $hover; } }

//Add Location
.hl_agency-location--add {
  .hl_controls--left {
    display: flex;
    align-items: center;
    .back {
      font-size: $f20;
      margin-right: 15px; } }
  .card {
    .card-header,
    .card-body, {
      padding-left: 40px;
      padding-right: 40px; } }
  .form-group {
    .business-category {
      position: relative;
      > a {
        color: #b0b8bc;
        font-size: $f12;
        position: absolute;
        top: 50%;
        right: -18px;
        transform: translateY(-50%);
        &:hover {
          color: $text-drk; } }
      + .business-category {
        margin-top: 10px; } } } }

.hours-item {
  margin-bottom: 20px;
  @media (min-width: $sm-min) {
    display: flex;
    align-items: center; }
  .option {
    width: 120px;
    min-width: 120px; }
  .dash {
    display: block;
    width: 8px;
    min-width: 8px;
    height: 1px;
    background-color: #728091;
    @media (min-width: $sm-min) {
      margin-left: 10px;
      margin-right: 10px; } } }

.hl_agency-location--add-sidebar {
  .card-body {
    padding: 20px!important;
    h4 {
      font-size: $f14;
      font-weight: 500; }
    p {
      font-size: 12px;
      + p {
        margin-top: 0; } }
    ul {
      margin-top: 10px;
      li {
        margin-right: 15px;
        a {
          img {
            max-width: 13px;
            height: auto; } } } } } }
.side-map {
  width: 100%;
  height: 240px;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px; }


//Location Details
.hl_agency-location--details {
  .hl_controls--left {
    display: flex;
    align-items: center;
    .back {
      font-size: $f20;
      margin-right: 15px; } }
  .product-revenue {
    padding-left: 10px;
    border-left: 3px solid $green;
    h4 {
      font-size: $f12;
      font-weight: normal;
      color: $hl-text;
      margin-bottom: 5px; }
    p {
      font-size: $f18;
      color: $hl-text;
      line-height: 1;
      color: $text-drk; } } }

.hl_activity-history {
  margin-top: 40px;
  h3 {
    font-size: 14px;
    font-weight: 500;
    margin-bottom: 25px; }
  &--item {
    margin-bottom: 35px;
    position: relative;
    padding-left: 50px;
    .icon {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 36px;
      height: 36px;
      border-radius: 50%;
      background-color: $text-drk;
      color: $white;
      position: absolute;
      top: -2px;
      left: 0;
      &.--blue {
        background-color: $blue; }
      &.--yellow {
        background-color: $yellow; }
      &.--green {
        background-color: $green; }
      &.--red {
        background-color: $red; }
      &.--orange {
        background-color: $orange; }
      &.--purple {
        background-color: $purple; }
      &.--teal {
        background-color: $teal; }
      &.--pink {
        background-color: $pink; } }
    &-header {
      margin-bottom: 10px;
      h4 {
        font-size: 14px;
        font-weight: 500;
        margin-bottom: 3px;
        span {
          color: $text;
          font-weight: normal; } }
      p {
        strong {
          color: $text-drk;
          font-weight: normal; } } }
    &-footer {
      display: flex;
      margin-top: 5px;
      p {
        strong {
          color: $text-drk; }
        + p {
          margin-top: 0; }
        &:not(:last-child) {
          margin-right: 15px; } } }
    p {
      font-size: $f12;
      line-height: 1.5; } } }


.hl_tasks {
  &--item {
    position: relative;
    padding-left: 35px;
    font-size: $f12;
    margin-bottom: 25px;
    &.--late {
      .hl_tasks--item-header {
        p {
          color: $red; } }
      .option {
        label {
          &:before {
            border-color: $red; } } } }
    &.--due {
      .hl_tasks--item-header {
        p {
          color: $yellow; } }
      .option {
        label {
          &:before {
            border-color: $yellow; } } } }
    &.--done {
      color: $hl-text-lt;
      .hl_tasks--item-header {
        h4 {
          color: $hl-text-lt;
          text-decoration: line-through; } }
      .hl_tasks--item-footer {
        p {
          strong {
            color: $hl-text-lt; } } } }
    .option {
      position: absolute;
      top: 4px;
      left: 0; }
    &-header {
      display: flex;
      align-items: flex-end;
      margin-bottom: 5px;
      h4 {
        font-size: 14px;
        font-weight: 500;
        margin-bottom: 0;
        margin-right: 20px;
        line-height: 1; }
      p {
        font-weight: 500;
        line-height: 1; } }
    &-footer {
      display: flex;
      margin-top: 0;
      p {
        margin-right: 20px;
        + p {
          margin-top: 0; }
        strong {
          font-weight: 500;
          color: $text-drk; } } } } }

.hl_products {
  .icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 36px;
    height: 36px;
    border-radius: 50%;
    margin-right: 15px;
    background-color: $text;
    &.--green {
      background-color: $green; }
    &.--yellow {
      background-color: $yellow; }
    &.--blue {
      background-color: $blue; }
    &.--pink {
      background-color: $pink; } }
  &--item {
    position: relative;
    padding-left: 50px;
    font-size: $f12;
    margin-bottom: 25px;
    .icon {
      position: absolute;
      top: 0;
      left: 0; }
    &-body {
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-wrap: wrap; }
    &-text {
      margin-right: 20px;
      h4 {
        margin-bottom: 4px;
        font-size: $f14;
        font-weight: 500; }
      .list-inline {
        &-item {
          line-height: 1.4;
          &:not(:last-child) {
            padding-right: 10px;
            border-right: 1px solid #e4e7ea; }
          strong {
            color: $green; } } } } } }

.hl_agency-billing-btns {
  .btn {
    margin-left: 10px; } }

.hl_agency-billing--table {
  .table {
    tbody {
      tr {
        td {
          &:first-child {
            font-weight: 500; }
          &:last-child {
            @media (min-width: $lg-min) {
              width: 80px; } } } } } } }

.hl_agency-sales-resources--item {
  .card-body {
    padding-top: 30px;
    padding-bottom: 30px;
    @media (min-width: $sm-min) {
      display: flex;
      align-items: center;
      justify-content: space-between; }
    > img {
      width: 100px;
      height: auto;
      margin-bottom: 15px;
      @media (min-width: $sm-min) {
        margin-bottom: 0;
        margin-right: 30px; } }
    .card-text {
      margin-bottom: 15px;
      @media (min-width: $sm-min) {
        margin-bottom: 0;
        margin-right: 30px; }
      h3 {
        font-size: 18px;
        font-weight: 500;
        color: $blue; } }
    .card-btns {
      max-width: 150px;
      margin: 0 auto;
      .btn {
        margin-bottom: 15px; } } } }
