/* ////========== WORKFLOW STYLES =========//// */

.hl_wrapper {
  &.d-flex {
    align-items: stretch; }
  &.nav-shrink {
    .hl_wrapper--inner {
      &.workflow {
        .hl_workflow--main {
          @media (min-width:$xl-min) {
            left: 70px; } } } } } }

.hl_wrapper--inner {
  &.workflow {
    flex: 1 0 0;
    min-height: 100%;
    padding-right: 280px;
    overflow: hidden;
    transition: $hover;
    &.sidebar-hide {
      padding-right: 0; } } }

//main
.hl_workflow--main {
  padding: 20px;
  overflow: auto;
  margin-top: -25px;
  //border: 1px solid blue //guide
  @media (min-width:$md-min) {
    margin: auto;
    position: fixed;
    top: 82px;
    left: 70px;
    right: 280px;
    bottom: 0;
    transition: $hover;
    &.sidebar-hide {
      right: 0; } }
  @media (min-width:$xl-min) {
    left: 250px; }
  .popover {
    border: none;
    bow-shadow: none;
    margin-bottom: 15px;
    box-shadow: 10px 10px 40px 0 rgba(0, 0, 0, 0.07);
    margin-right: -230px;
    .arrow {
      display: block;
      margin-right: -230px;
      &:before {
        border: none; } }
    .popover-body {
      padding: 0;
      bow-shadow: none; } }

  //flow
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    margin: 0 -10px;
    li {
      margin: 0 10px 20px 10px;
      display: flex;
      justify-content: center;
      align-items: flex-start;
 }      //border: 1px solid red //guide
    //main
    &.flow_main {
      min-width: 1400px;
      > li {
        > .flow_split { //first-split
          > li {
            min-width: 600px;
            flex: 1 0 0!important;
            > .split-left,
            > .split-right {
              height: 100px;
              top: -100px;
              svg {
                height: 100px; } } } } } }
    //start
    &.flow_start {
      margin-top: 0!important;
      margin-bottom: 60px;
      align-items: center;
      display: flex;
      justify-content: center;
      > li {
        flex: 0 0 auto!important; } }
    //inner
    &.flow_inner {}

    &.flow_split {
      //border: 1px solid green //guide
      margin-top: 60px;
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      > li {
        flex: auto 0 0!important;
        position: relative;
        .split-left {
          display: block;
          width: calc(50% + 10px);
          height: 80px;
          position: absolute;
          top: -80px;
          left: 50%;
          svg {
            width: 100%;
            height: 80px;
            path {
              vector-effect: non-scaling-stroke;
              fill: none;
              stroke: darken($border, 20%);
              stroke-width: 2;
              stroke-dasharray: 8 4; } } }
        .split-right {
          display: block;
          width: calc(50% + 10px);
          height: 80px;
          position: absolute;
          top: -80px;
          right: 50%;
          svg {
            width: 100%;
            height: 80px;
            path {
              vector-effect: non-scaling-stroke;
              fill: none;
              stroke: darken($border, 20%);
              stroke-width: 2;
              stroke-dasharray: 8 4; } } } } } } }


.workflow_card--new {
  max-width: 400px;
  display: flex;
  justify-content: center;
  .bubble {
    display: inline-block;
    border: 2px dashed darken($border, 8%);
    max-width: 150px;
    background: transparent;
    border-radius: 4px;
    padding: 15px 20px;
    text-align: center;
    font-size: $f14;
    line-height: 1.4;
    p {
      font-weight: 500;
      color: $text; } } }


.workflow_card {
  display: flex;
  justify-content: center;
  max-width: 400px;
  .bubble {
    display: inline-block;
    background-color: $white;
    border-radius: 4px;
    padding: 15px 20px;
    color: $text-drk;
    text-align: center;
    font-size: $f14;
    line-height: 1.4;
    position: relative;
    max-width: 400px;
    p {
      strong {
        font-weight: 500; } } }
  &.--with-icon {
    .bubble {
      position: relative;
      font-size: $f20;
      padding-left: 50px;
      text-align: left;
      .svg-inline--fa {
        position: absolute;
        top: 15px;
        left: 20px; }
      p {
        font-size: $f14;
        font-weight: 500;
 }        //white-space: nowrap
      &:hover {
        .icon-drag {
          opacity: 1; } } } }
  &.--blue {
    .bubble {
      background-color: rgba($blue, 0.7);
      color: $white; } }
  &.--green {
    .bubble {
      background-color: rgba($green, 0.7);
      color: $white; } }
  &.--yellow {
    .bubble {
      background-color: rgba($yellow, 0.7);
      color: $white; } }
  &.--red {
    .bubble {
      background-color: rgba($red, 0.7);
      color: $white; } }
  &.--grey {
    .bubble {
      background-color: rgba($text, 0.7);
      color: $white; } }
  &.--white {
    .bubble {
      background-color: $white;
      color: $text;
      p {
        font-weight: normal; } } }
  .icon-drag {
    display: block;
    font-size: $f12;
    color: $text-drk;
    position: absolute;
    top: 50%;
    right: -15px;
    transform: rotate(45deg) translateY(-50%);
    opacity: 0;
    transition: $hover; }
  + {
    .workflow_card--add {
      margin-top: 20px; } } }

.workflow_actions {
  padding: 8px 15px;
  margin: 0;
  ul {
    margin: 0;
    display: flex;
    align-items: center;
    li {
      margin-bottom: 0!important;
      &:not(:last-child) {
        margin-right: 15px; } } } }

.workflow_card--add {
  max-width: 400px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  @include center-align;
  &:before {
    content: "";
    display: block;
    width: 1px;
    height: 20px;
    border-left: 2px dashed darken($border, 20%);
    margin: auto;
    position: absolute;
    top: -20px;
    left: 0;
    right: 0; }
  &:after {
    content: "";
    display: block;
    width: 1px;
    height: 20px;
    border-left: 2px dashed darken($border, 20%);
    margin: auto;
    position: absolute;
    left: 0;
    right: 0;
    bottom: -20px; }
  &.--start {
    &:before {
      display: none; } }
  &.--end {
    &:after {
      display: none; } }
  > a {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: $white;
    border: 1px solid darken($border, 5%);
    &:hover {
      box-shadow: 5px 5px 20px 0 rgba(0, 0, 0, 0.1); } }
  + {
    .workflow_card {
      margin-top: 20px; } } }


.workflow_card--yes {
  max-width: 400px;
  min-width: 400px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  @include center-align;
  > a,
  span {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: $white;
    color: $green;
    font-weight: bold;
    border: 1px solid $green;
    &:hover {
      box-shadow: 5px 5px 20px 0 rgba(0, 0, 0, 0.1); } }
  + {
    .workflow_card--add {
      margin-top: 20px; } } }

.workflow_card--no {
  max-width: 400px;
  min-width: 400px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  @include center-align;
  > a,
  span {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: $white;
    color: $red;
    font-weight: bold;
    border: 1px solid $red;
    &:hover {
      box-shadow: 5px 5px 20px 0 rgba(0, 0, 0, 0.1); } }
  + {
    .workflow_card--add {
      margin-top: 20px; } } }


//sidebar
.hl_workflow--sidebar {
  width: 280px;
  background-color: #E7F3FE;
  padding-top: 121px;
  position: absolute;
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  transition: $hover;
  @media (min-width:$md-min) {
    position: fixed;
    top: 82px; }
  &.hide {
    transform: translateX(280px); }
  .hide-sidebar {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #E7F3FE;
    width: 40px;
    height: 40px;
    color: $text-drk;
    font-size: $f12;
    position: absolute;
    top: 0;
    left: -40px;
    &:hover {
      color: $blue; }
    .icon {
      transition: $hover; }
    &.active {
      .icon {
        transform: rotate(-180deg); } } }
  &-header {
    background-color: #E7F3FE;
    padding: 20px;
    position: absolute;
    margin: auto;
    top: 0;
    left: 0;
    right: 0;
    h2 {
      font-size: $f18;
      margin-bottom: 20px; } }
  &-inner {
    padding: 0 20px 20px 20px;
    height: 100%;
    overflow-y: auto; } }

.actions-set {
  margin-bottom: 10px;
  > a {
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: $blue;
    padding-top: 5px;
    padding-bottom: 5px;
    &:hover {
      color: $blue; }
    &.collapsed {
      color: $text;
      .caret {
        transform: none; } }
    .caret {
      font-size: $f10;
      transition: inheret;
      transform: rotate(-180deg); } }
  .actions-list {
    list-style: none;
    margin: 0;
    margin-top: 10px;
    padding: 0;
    li {
      background-color: $white;
      border-radius: 4px;
      padding: 10px 15px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      cursor: all-scroll;
      &:not(:last-child) {
        margin-bottom: 10px; }
      .icon {
        transform: rotate(45deg);
        font-size: $f10; } } } }


//new automation modal
.modal {
  &.new-automation-modal {
    .modal-header {
      padding-top: 15px;
      padding-bottom: 15px; }
    .modal-header--inner {
      display: flex;
      align-items: center;
      justify-content: space-between; }
    .search-form {
      margin-right: 30px;
      .form-control {
        background-color: rgba(24,139,246,0.05)!important; } }
    .modal-body {
      padding: 0;
      display: flex;
      > * {
        flex: 1 0 0;
        max-height: 550px;
        overflow-y: auto; } } } }
.recipe-stats {
  list-style: none;
  padding: 0;
  margin: 0;
  li {
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:not(:last-child) {
      margin-bottom: 5px; }
    .icon {
      font-size: $f14;
      display: inline-block;
      margin-right: 5px; }
    .number {
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 4px;
      width: 26px;
      height: 28px;
      color: $green;
      font-size: $f14;
      background-color: rgba($green, 0.2); } } }
.categories-menu {
  max-width: 250px;
  padding: 20px;
  background-color: darken(#f2f7fa, 3%);
  border-bottom-left-radius: 6px;
  hr {
    margin-top: 25px;
    margin-bottom: 25px;
    border-color: darken($border, 5%); }
  h4 {
    font-size: $f16;
    margin-bottom: 20px; } }
.categories-menu-list {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-bottom: 20px;
  > li {
    margin-bottom: 2px;
    > a {
      color: $text;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-left: -20px;
      margin-right: -20px;
      padding: 5px 20px;
      &:hover {
        background-color: rgba($blue, 0.1);
        color: $blue; }
      &.collapse-nav {
        background-color: rgba($blue, 0.1);
        color: $blue;
        .caret {
          font-size: $f10;
          transition: $hover;
          transform: rotate(-180deg); }
        &.collapsed {
          background-color: transparent;
          color: $text;
          &:hover {
            background-color: rgba($blue, 0.1);
            color: $blue; }
          .caret {
            transform: none; } } } }
    > ul {
      list-style: none;
      padding-left: 10px;
      margin: 0;
      li {
        padding-top: 2px;
        padding-bottom: 2px;
        &:first-child {
          padding-top: 5px; }
        &:last-child {
          padding-bottom: 10px; }
        a {
          color: $text;
          font-size: $f13;
          &:hover {
            color: $blue; } } } } } }
.automation-list-wrap {
  position: relative; }
.automation-import {
  padding: 10px 20px;
  background-color: lighten(#f2f7fa, 1%);
  position: absolute;
  margin: auto;
  top: 0;
  left: 250px;
  right: 0;
  z-index: 2;
  .btn {
    font-size: $f12!important;
    min-width: auto!important;
    padding: 9px 15px!important;
    .icon {
      display: inline-block;
      margin-right: 5px; } } }
.automation-continue {
  padding: 10px 20px;
  background-color: lighten(#f2f7fa, 1%);
  position: absolute;
  margin: auto;
  left: 250px;
  right: 0;
  bottom: 0;
  z-index: 2;
  text-align: right;
  .btn {
    font-size: $f12!important;
    min-width: auto!important;
    padding: 9px 15px!important;
    font-weight: 500!important; } }
.automation-list {
  list-style: none;
  padding: 68px 20px 66px 20px;
  margin: 0;
  li {
    padding: 20px;
    margin-bottom: 15px;
    border-radius: 6px;
    transition: $hover;
    cursor: pointer;
    border: 1px solid #f2f7fa;
    position: relative;
    &:hover {
      background-color: #f2f7fa;
      .favorite {
        background-color: $white; } }
    &.active {
      border: 1px solid rgba($blue, 0.3);
      background-color: rgba($blue, 0.1);
      .favorite {
        background-color: $white; } }
    h3 {
      font-size: $f16;
      margin-bottom: 15px; }
    p {
      font-size: $f14;
      line-height: 1.6;
      span {
        display: inline; } }
    .morecontent {
      span {
        display: none; } }
    .category {
      font-size: $f12;
      font-weight: 500;
      .fas {
        display: inline-block;
        margin-right: 5px; } }
    .favorite {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 45px;
      height: 25px;
      border-radius: 30px;
      background-color: #f2f7fa;
      text-align: center;
      font-size: $f13;
      position: absolute;
      top: 20px;
      right: 20px;
      &.active {
        transition: $hover;
        background-color: lighten($red, 15%);
        color: $white; } } } }
