/* ////========== CONTACT DETAILS STYLES =========//// */

.hl_contact-details-new {
  padding: 1px 0 0 0;
  overflow: hidden;
  overflow-x: scroll; }

.hl_contact-details-new--wrap {
  min-width: 1250px;
  height: 100%;
  display: flex;
  height: calc(100vh - 110px);
  @media(min-width: $md-min) {
    height: calc(100vh - 97px); }
  > * {
    flex: 1 0 0; }

  //left
  .hl_contact-details-left {
    max-width: 380px;
    height: 100%;
    background-color: $white;
    position: relative;
    z-index: 9;
    border-left: 2px solid $border;
    border-right: 1px solid $border;
    padding: 20px;
    h2 {
      font-size: $f16;
      margin-bottom: 20px;
      .back {
        display: inline-block;
        margin-right: 10px; } }
    .hl_contact-details-left-tabs {
      position: absolute;
      margin: auto;
      top: 55px;
      left: 0;
      right: 0;
      bottom: 50%;
      .nav-tabs {
        padding-left: 20px;
        padding-right: 20px; }
      .tab-content {
        position: relative;
        height: 100%;
        overflow: hidden;
        .tab-pane {
          position: absolute;
          margin: auto;
          top: 0;
          left: 0;
          right: 0;
          bottom: 30px;
          overflow-y: auto;
          padding: 20px; } } }
    .hl_activity-history {
      position: absolute;
      margin: auto;
      top: 50%;
      left: 0;
      right: 0;
      bottom: 0;
      overflow-y: auto;
      padding: 20px;
      border-top: 1px solid $border; } }


  //right
  .hl_contact-details-right {
    max-width: 380px;
    height: 100%;
    background-color: $white;
    position: relative;
    z-index: 9;
    border-left: 1px solid $border;
    padding: 20px;
    .hl_contact-details-right-tabs {
      position: absolute;
      margin: auto;
      top: 24px;
      left: 0;
      right: 0;
      bottom: 210px;
      border-bottom: 1px solid $border;
      transition: $hover;
      .nav-tabs {
        padding-left: 20px;
        padding-right: 20px; }
      .tab-content {
        position: relative;
        height: 100%;
        overflow: hidden;
        .tab-pane {
          position: absolute;
          margin: auto;
          top: 0;
          left: 0;
          right: 0;
          bottom: 30px;
          overflow-y: auto;
          padding: 20px; } }
      &.expand {
        bottom: 550px; } }
    .hl_contact-details-right-tabs-bottom {
      position: absolute;
      margin: auto;
      height: 185px;
      left: 0;
      right: 0;
      bottom: 0;
      transition: $hover;
      .nav-tabs {
        padding-left: 20px;
        padding-right: 20px; }
      .tab-content {
        position: relative;
        height: 100%;
        overflow: hidden;
        .tab-pane {
          position: absolute;
          margin: auto;
          top: 0;
          left: 0;
          right: 0;
          bottom: 20px;
          overflow-y: auto;
          padding: 20px; } }
      &.expand {
        height: 525px; } } } }










.hl_contact-details {
  .container-fluid {
    max-width: 1920px;
    > .row {
      @media (min-width: $xl-min) and (max-width: $xl-max) {
        margin-left: -10px;
        margin-right: -10px;
        > div {
          padding-left: 10px;
          padding-right: 10px; } } } } }

.hl_contact-messages {
  @media (min-width: $xl-min) {
    margin-bottom: 0; }
  .card-body {
    padding-bottom: 265px;
    position: relative;
    &.email-box-active {
      padding-bottom: 460px;
      .messages-group {
        min-height: 280px;
        max-height: calc(100vh - 620px); } } }
  .message-input-wrap {
    padding-top: 20px;
    position: absolute;
    margin: auto;
    bottom: 0px;
    left: 0px;
    right: 0px;
    padding-left: 30px;
    padding-right: 30px;
    padding-bottom: 20px;
    box-shadow: 0 8px 16px 10px rgba(0, 0, 0, 0.02);
    background: #fff;
    z-index: 2;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px; }
  .messages-group {
    min-height: 460px;
    max-height: calc(100vh - 425px); }

  .message-box {
    position: relative;
    margin: auto;
    background: #fff;
    z-index: 2;
    padding-bottom: 10px;
    .form-control {
      resize: none;
      &.drag-active {
        background: darken(#f7fafc, 5%); } }
    .message-action-top {
      display: flex;
      align-items: center;
      position: absolute;
      top: 10px;
      right: 10px;
      > * {
        &:not(:last-child) {
          margin-right: 10px; } } }
    .message-action-bottom {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: 10px; }
    .btn-blue {
      height: 40px;
      display: flex;
      align-items: center;
      .icon-send {
        display: inline-block;
        width: 14px;
        height: 14px;
        background: url('../img/icon-send-white.svg')no-repeat center;
        background-size: 14px;
        margin-right: 5px;
        position: relative;
        top: -5px; }
      span {
        display: inline-block; } }
    .file-open {
      > input {
        display: none; }
      label {
        cursor: pointer;
        opacity: 0.6;
        transition: $hover;
        margin-bottom: 0;
        &:hover,
        &:active,
        &:focus {
          opacity: 1; } } }
    .emoji-open {
      opacity: 0.6;
      &:hover,
      &:active,
      &:focus {
        opacity: 1; } }
    .use-template {
      display: inline-block;
      font-weight: 500;
      color: #607179;
      opacity: 0.6;
      &:hover,
      &:active,
      &:focus {
        opacity: 1; }
      .icon {
        font-size: 16px; } } }

  .email-box {
    .form-group {
      margin-bottom: 10px; }
    .email-action-bottom {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: space-between;
      margin-top: 10px;
      > div {
        margin-bottom: 10px; }
      &-right {
        display: flex;
        align-items: center; }
      .file-open {
        > input {
          display: none; }
        label {
          cursor: pointer;
          opacity: 0.6;
          transition: $hover;
          font-weight: 500;
          font-size: $f14;
          &:hover,
          &:active,
          &:focus {
            opacity: 1; } } }
      .btn-blue {
        height: 40px;
        display: flex;
        align-items: center;
        .icon-send {
          display: inline-block;
          width: 14px;
          height: 14px;
          background: url('../img/icon-send-white.svg')no-repeat center;
          background-size: 14px;
          margin-right: 5px;
          position: relative;
          top: -5px; } }
      .use-template {
        display: inline-block;
        font-weight: 500;
        color: #607179;
        opacity: 0.6;
        &:hover,
        &:active,
        &:focus {
          opacity: 1; }
        .icon {
          font-size: 16px; } } } } }



.hl_sms-template--modal,
.hl_email-template--modal {
  .list-group {
    .list-group-item {
      > a {
        color: $text; } } }
  .search-form {
    position: relative;
    .icon {
      position: absolute;
      bottom: 18px;
      left: 15px; }
    .form-control {
      padding-left: 40px; } } }


.email-item {
  position: relative;
  padding-left: 55px;
  padding-bottom: 20px;
  &:not(:last-child) {
    &:before {
      content: "";
      display: block;
      width: 2px;
      background-color: $border;
      position: absolute;
      margin: auto;
      top: 0;
      left: 17px;
      bottom: 0; } }
  > .icon-mail {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 35px;
    height: 35px;
    border-radius: 50%;
    background-color: #E9F3FF;
    color: $blue;
    font-size: $f12;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 3; }
  &--header {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    border-bottom: 2px solid $border;
    > * {
      margin-bottom: 10px; }
    &-left {
      margin-right: 20px;
      h4 {
        margin-bottom: 0;
        font-size: $f14;
        color: $text;
        strong {
          font-weight: 500;
          color: $text-drk; } }
      p {
        font-size: $f13;
        .label {
          display: inline-block;
          color: $green;
          background-color: rgba($green, 0.2);
          line-height: 1;
          font-size: $f10;
          font-weight: 500;
          padding: 3px 10px;
          border-radius: 20px; } }
      h5 {
        font-size: $f14;
        font-weight: 500;
        margin-bottom: 0; } } }
  &--body {
    padding-top: 15px;
    padding-bottom: 15px;
    p {
      font-size: $f13;
      + p {
        margin-top: 5px; }
      .icon {
        color: $text-lt;
        font-size: $f16; } }
    .more-thread {
      > a {
        display: block;
        font-size: $f13;
        padding: 10px 15px;
        border: 1px solid darken($border, 3%);
        background-color: lighten($border, 3%);
        border-radius: 4px;
        &:hover {
          background-color: lighten($border, 2%); } } }
    .more-thread-preview {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      font-size: $f13;
      padding: 10px 15px;
      border: 1px solid darken($border, 3%);
      background-color: lighten($border, 3%);
      border-radius: 4px;
      cursor: pointer;
      &:not(:last-child) {
        margin-bottom: 10px; }
      &:hover {
        background-color: lighten($border, 2%); }
      h4 {
        font-size: $f13;
        font-weight: 500;
        span {
          font-weight: normal;
          color: $text; }
        .icon-mail {
          display: inline-block;
          font-size: $f12;
          color: rgba($blue, 0.9);
          margin-right: 5px; } }
      p {
        color: $text-lt; } }
    .more-thread-item {
      padding: 20px;
      border: 1px solid darken($border, 3%);
      background-color: lighten($border, 3%);
      border-radius: 4px;
      &:not(:last-child) {
        margin-bottom: 10px; }
      .email-item--body {
        padding-bottom: 0; } } } }
