/* ////========== OPPORTUNITIES STYLES =========//// */


.hl_opportunities {
  @media (min-width: $lg-min) {
    .row {
      margin-left: -5px;
      margin-right: -5px;
      > div {
        padding-left: 5px;
        padding-right: 5px; } } } }

.hl_opportunities--set {
  margin-top: 20px;
  &-header {
    margin-bottom: 20px;
    h4 {
      font-size: 18px;
      margin-bottom: 1px; }
    p {
      display: flex;
      flex-wrap: wrap;
      //justify-content: space-between
      align-items: center;
      strong {
        display: inline-block;
        margin-right: 10px;
        font-weight: normal; }
      span {
        display: inline-block;
        color: $green; } } }
  &.--prospects {
    .hl_opportunities-item {
      border-top: 3px solid $blue; } }
  &.--appointments {
    .hl_opportunities-item {
      border-top: 3px solid $orange; } }
  &.--consults {
    .hl_opportunities-item {
      border-top: 3px solid $purple; } }
  &.--close {
    .hl_opportunities-item {
      border-top: 3px solid $teal; } }
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    margin-bottom: 10px;
    @media (min-width: $sm-min) and (max-width: $md-max) {
      max-height: 405px;
      overflow-y: auto; }
    @media (min-width: $lg-min) {
      max-height: calc(100vh - 355px);
      overflow-y: auto; } } }
.hl_opportunities-item {
  margin-bottom: 10px;
  cursor: grab;
  &.ui-draggable-dragging {
    min-width: 280px;
    cursor: grabbing;
    transform: rotate(5deg);
    z-index: 9999999; }
  &.active {
    position: absolute;
    cursor: grabbing; }
  h5 {
    font-size: 15px;
    margin-bottom: 0;
    line-height: 1;
    font-weight: 500;
    .new {
      display: inline-block;
      padding: 3px 7px;
      background-color: rgba($blue, 0.1);
      color: $blue;
      border-radius: 4px;
      font-size: $f10;
      position: relative;
      top: -2px; } }
  .card-body {
    padding: 20px; }
  .card-info {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    &:not(:last-child) {
      margin-bottom: 10px; }
    p {
      + p {
        margin-top: 0; } }
    .value {
      color: $green; }
    .avatar {
      margin-right: 10px;
      h4 {
        font-size: 12px; } }
    .date {
      font-size: 11px;
      line-height: 1;
      background-color: rgba($text, 0.1);
      color: $text;
      padding: 5px 10px;
      border-radius: 30px; }
    .product {
      color: $text-drk; }
    .btn:only-child {
      margin-left: auto; } } }
.hl_add-opportunities--modal {
  h3 {
    font-size: 16px;
    margin-top: 10px;
    margin-bottom: 20px; }
  .form-group {
    .row {
      margin-left: -5px;
      margin-right: -5px;
      > div {
        padding-left: 5px;
        padding-right: 5px;
        > p {
          line-height: 50px; } } } }
  .tag-group {
    margin-top: 10px; } }



.hl_opportunities--drag-actions {
  background: rgba($blue, 0.1);
  position: fixed;
  margin: auto;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999999;
  opacity: 0;
  transition: $hover;
  &.active {
    transform: none;
    opacity: 1; }
  @media(min-width: $md-min) {
    left: 70px; }
  @media(min-width: $xl-min) {
    left: 250px; }
  &-inner {
    padding: 40px 15px 30px 15px;
    max-width: 1200px;
    @include center-align();
    @media(min-width: $md-min) {
      display: flex;
      align-items: center;
      > * {
        flex: 1 0 0;
        margin-left: -10px;
        margin-right: -10px; } } }
  .drag-action {
    padding: 25px;
    border-radius: 4px;
    color: $white;
    text-align: center;
    transition: $hover;
    margin-bottom: 10px;
    @media(min-width: $md-min) {
      padding: 35px;
      margin-bottom: 0;
      margin-left: 10px;
      margin-right: 10px; }
    h4 {
      color: $white;
      text-transform: uppercase;
      font-size: $f16;
      margin-bottom: 0;
      @media(min-width: $md-min) {
        font-size: $f20; } }
    &.--lost {
      background-color: lighten($red, 5%);
      &:hover,
      &.active {
        background-color: darken($red, 10%); } }
    &.--abandoned {
      background-color: $white;
      &:hover,
      &.active {
        background-color: darken($hl-bg, 10%); }
      h4 {
        color: $text; } }
    &.--won {
      background-color: lighten($green, 5%);
      &:hover,
      &.active {
        background-color: darken($green, 10%); } } } }
