/* ////========== CUSTOMERS STYLES =========//// */

//customer details
.customers-details {
  position: relative;
  max-width: 1600px;
  @include center-align();
  @media (min-width: $lg-min) {
    padding-right: 400px; } }

//table
.hl_customers--table {
  margin-top: 20px;
  .table {
    tbody {
      tr {
        td {
          &:last-child {
            @media (min-width: $lg-min) {
              width: 130px; }
            .btn-green-lt {
              margin-right: 10px; } } } } } } }




//Reviews
.hl_customers--reviews {
  .card-body {
    padding-top: 40px; }
  h3 {
    font-size: $f50;
    font-weight: 300;
    line-height: 1;
    margin-bottom: 30px;
    vertical-align: top;
    .rating {
      display: inline-block;
      position: relative;
      top: 5px;
      .icon {
        vertical-align: top; } } }
  .card-footer {
    padding: 0 30px 30px 30px;
    border-top: none;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: flex-end;
    .card-footer--left {
      margin-right: 20px;
      a {
        color: $text;
        &:hover,
        &:active {
          color: $blue; } }
      .respond-toggle {
        span {
          display: none; }
        &:before {
          content: "Close Comments"; }
        &.collapsed {
          &:before {
            content: "Respond"; } } }
      .spacer {
        display: inline-block;
        margin: 0 10px;
        color: $hl-text-lt;
        font-size: $f18;
        transform: translateY(3px); } }
    .card-footer--right {
      display: flex;
      align-items: center;
      > * + * {
        margin-left: 10px; }
      .comment-count {
        .icon {
          color: rgba($text, 0.5);
          display: inline-block;
          margin-right: 5px; }
        span {
          font-size: $f12; } }
      .bootstrap-select {
        &.more-select {
          > .dropdown-menu {
            @media (min-width: $lg-min) {
              left: auto!important;
              right: 0!important;
              transform: translateX(0) translateY(40px)!important; } } } } } }
  .review {
    margin-bottom: 30px;
    display: flex;
    align-items: center;
    > img {
      width: 50px;
      min-width: 50px;
      max-width: 50px;
      height: 50px;
      max-height: 50px;
      margin-right: 20px; } }
  .avatar {
    margin-top: 20px; } }


//checkins
.hl_customers--checkins {
  .card-header {
    padding: 15px 30px; }
  .table {
    tbody {
      tr {
        td {
          &:last-child {
            @media (min-width: $lg-min) {
              width: 250px; } } } } } } }


//Review Attemps
//checkins
.hl_customers--review-attemps {
  .card-header {
    padding: 15px 30px;
    .btn {
      display: inline-flex;
      align-items: center;
      .icon {
        margin-right: 10px; } } }
  .table {
    tbody {
      tr {
        td {
          &:last-child {
            @media (min-width: $lg-min) {
              width: 100px; } } } } } } }

//sidebar
.hl_customers--sidebar {
  background: #fff;
  padding: 40px 30px;
  @media (min-width: $lg-min) {
    width: 380px;
    position: absolute;
    margin: auto;
    top: 0;
    right: 0;
    bottom: 0; }
  //inner
  &-inner {
    @media (min-width: $lg-min) {
      max-width: 290px;
      @include center-align(); } }
  //avatar
  .sidebar_avatar-name {
    text-align: center;
    margin-bottom: 20px;
    .avatar {
      margin-bottom: 20px;
      @include center-align(); }
    h3 {
      font-size: $f18;
      line-height: 1;
      margin-bottom: 0; } }
  //buttons
  .btns {
    text-align: center;
    .btn {
      padding: 8px 20px;
      margin-left: 5px;
      margin-right: 5px;
      .icon {
        margin-right: 5px; } } }
  //info
  .sidebar_info-item {
    &:not(last-child) {
      margin-bottom: 20px; }
    h4 {
      font-size: $f14;
      color: $text;
      margin-bottom: 5px; }
    p {
      color: $text-drk; } }
  hr {
    margin-top: 30px;
    margin-bottom: 30px; }
  //notes
  .sidebar_notes-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    h3 {
      font-size: $f14;
      color: $text;
      margin-bottom: 0; }
    a {
      font-size: $f14; } }
  .sidebar_notes-item {
    &:not(last-child) {
      margin-bottom: 20px; }
    p {
      color: $text-drk; }
    span {
      color: $text; } } }



//import modal
.hl_import-customers {
  .modal-body {
    .modal-body--inner {
      max-width: 100%; } }
  .hl_import-customers--steps1,
  .hl_import-customers--steps2,
  .hl_import-customers--steps3,
  .hl_import-customers--steps4,
  .hl_import-customers--steps5 {
    max-width: 600px;
    @include center-align(); } }
.hl_import-customers--steps-heading {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
  margin-top: -30px;
  li {
    background-color: rgba($green, 0.1);
    display: block;
    width: 30px;
    height: 30px;
    margin-left: 20px;
    margin-right: 20px;
    color: $green;
    text-align: center;
    line-height: 30px;
    border-radius: 50%;
    position: relative;
    &:after {
      content: "\e907";
      font-family: 'Magicons' !important;
      speak: none;
      font-style: normal;
      font-weight: normal;
      font-variant: normal;
      text-transform: none;
      line-height: 1;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
      color: $text-lt;
      font-size: 11px;
      position: absolute;
      top: 50%;
      right: -25px;
      transform: translateY(-50%); }
    &:last-child {
      &:after {
        display: none; } }
    &.active {
      background-color: $green;
      color: $white; } } }

.hl_import-customers--heading {
  margin-bottom: 30px;
  text-align: center;
  border-bottom: 2px solid $border;
  padding-bottom: 20px;
  margin-left: -15px;
  margin-right: -15px;
  h3 {
    font-size: 16px;
    margin-bottom: 5px; }
  p {
    font-size: 12px; } }

.upload-spreadsheet {
  text-align: center;
  padding-top: 100px;
  padding-bottom: 100px;
  > .icon {
    display: block;
    font-size: 40px;
    margin-bottom: 20px;
    color: $text-lt; }
  h4 {
    font-size: 14px;
    margin-bottom: 5px; }
  p {
    font-size: 12px;
    margin-bottom: 20px; }
  .upload-file {
    input {
      display: none;
      visibility: hidden; } } }

.hl_imported-file {
  margin-bottom: 40px;
  h3 {
    font-size: 13px;
    font-weight: 500;
    margin-bottom: 15px; }
  .the-uploaded-file {
    display: flex;
    align-items: center; }
  .the-file {
    display: flex;
    align-items: center;
    margin-right: 20px;
    background-color: rgba($green, 0.1);
    border-radius: 4px;
    padding: 5px 10px;
    .icon {
      display: flex;
      justify-content: center;
      align-items: center;
      color: $green;
      height: 35px;
      width: 35px; }
    span {
      display: inline-block;
      color: $text-drk;
      padding-left: 10px;
      padding-right: 10px; } }
  .upload-diffrent-file {
    input {
      display: none;
      visibility: hidden; }
    label {
      color: $blue;
      margin-bottom: 0;
      font-size: 14px;
      font-weight: 500; } } }

.hl_imported-file-map {
  h3 {
    font-size: 14px;
    font-weight: 500;
    margin-bottom: 15px; }
  .card {
    border: 2px solid #f2f7fa;
    //background-color: #f2f7fa
    // .bootstrap-select
    //   .dropdown-toggle
 } }    //     background-color: #fff

.field-mapping-preview {
  background-color: #f2f7fa;
  &--inner {
    padding: 20px; }
  h4 {
    padding: 20px;
    color: $blue;
    font-size: 16px;
    font-weight: 500;
    background-color: rgba($blue, 0.1); }
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    li {
      margin-bottom: 10px;
      strong {
        display: inline-block;
        width: 150px; } } } }

.existing-contacts {
  h4 {
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 20px; }
  .radio {
    margin-bottom: 10px;
    label {
      font-size: 14px;
      &:before {
        top: 4px; }
      &:after {
        top: 8px; } } } }

.add-new-contacts {
  h4 {
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 20px; }
  .form-control {
    margin-bottom: 15px;
    max-width: 350px; }
  .the-file {
    display: inline-flex;
    align-items: center;
    height: 40px;
    padding-left: 20px;
    background-color: rgba($green, 0.1);
    color: $green;
    font-weight: 500;
    border-radius: 4px;
    .remove {
      display: flex;
      align-items: center;
      justify-content: center;
      color: $green;
      font-size: 16px;
      margin-left: 20px;
      width: 40px;
      height: 40px;
      border-left: 1px solid rgba($green, 0.2);
      &:hover {
        background-color: rgba($green, 0.2); } } } }


//
.hl_add-tags {
  h4 {
    font-size: 18px; }
  .avatar-group {
    margin-bottom: 20px; }
  .tag-group {
    margin-top: 15px; } }
