/* ////========== NAV STYLES =========//// */

.hl_navbar {
  background: #fff; //temp
  width: 100%;
  padding: 25px 15px;
  transition: $basic;
  border-bottom: 2px solid $border;
  @media(max-width: $sm-max) {
    padding: 15px; }
  @media(min-width: $md-min) {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 999;
    padding: 25px 0;
    width: 70px;
    bottom: 0;
    border-bottom: none;
 }    //overflow: hidden
  @media(min-width: $xl-min) {
    width: 250px; }
  //shrink
  &.shrink {
    @media(min-width: $md-min) {
      width: 70px!important; }
    @media(min-width: $xl-min) {
      width: 70px!important; }
    .hl_navbar--button {
      @media(min-width: $xl-min) {
        font-size: 0!important;
        min-width: auto!important;
        height: 43px;
        width: 50px;
        padding-left: 0!important;
        padding-right: 0!important;
        position: relative;
        &:before {
          content: "\e960";
          display: block;
          font-family: 'Magicons' !important;
          speak: none;
          font-size: $f13;
          font-style: normal;
          font-weight: normal;
          font-variant: normal;
          text-transform: none;
          line-height: 13px;
          position: absolute;
          top: 50%;
          right: 50%;
          transform: translate(50%, -50%);
          color: #fff;
          transition: $hover; } } }
    .hl_navbar--links {
      @media(min-width: $xl-min) {
        > li {
          a {
            span {
              opacity: 0;
              transition: $hover;
              transform: translateX(30px); } }
          .caret {
            display: none!important; }
          .nav-marketing-links,
          .nav-dropdown-links {
            ul {
              @media (min-width: $md-min) {
                padding: 20px 10px!important; }
              li {
                a {
                  @media (min-width: $md-min) {
                    font-size: 10px!important;
                    line-height: 1.3!important;
                    text-align: center!important; } } } } } } } }
    .tooltip {
      display: block!important; } }



  //Logo
  &--logo {
    display: block;
    @media(min-width: $md-min) {
      @include center-align();
      margin-bottom: 35px;
      > svg {
        display: block;
        width: 28px;
        height: 26px;
        @include center-align(); }
      > img {
        display: block;
        width: auto;
        max-height: 32px;
        @include center-align(); } } }
  //Collapse
  &--collapse {
    @media(max-width: $sm-max) {
      border-top: 2px solid $border;
      padding-top: 15px;
      padding-left: 15px;
      padding-right: 15px;
      margin-top: 20px;
      margin-left: -15px;
      margin-right: -15px;
      display: none; }
    @media (min-width: $md-min) {
      display: block!important; } }
  //Toggler
  &--toggler {
    margin-top: 4px;
    margin-bottom: 4px;
    width: 26px;
    height: 26px;
    padding: 2px 0;
    border: none;
    position: absolute;
    top: 13px;
    right: 15px;
    @media (min-width: $md-min) {
      display: none;
      visibility: hidden;
      opacity: 0; }
    .navbar-toggler-bar {
      display: block;
      width: 26px;
      height: 2px;
      background: $text-lt;
      transition: $basic4;
      border-radius: 4px;
      margin-bottom: 5px;
      &:last-child {
        margin-bottom: 0; }
      @media (min-width: $lg-min) {
        display: none;
        visibility: hidden; } }
    &:hover,
    &:active {
      .navbar-toggler-bar {
        background: $text; } }
    &.active {
      .navbar-toggler-bar {
        background: $text;
        &:nth-child(1) {
          transform: rotate(45deg);
          position: relative;
          top: 7px; }
        &:nth-child(2) {
          opacity: 0; }
        &:nth-child(3) {
          transform: rotate(-45deg);
          position: relative;
          bottom: 7px; } } } }
  //Button
  &--button {
    font-size: $f14!important;
    width: 100%;
    @include center-align();
    margin-bottom: 20px;
    &:before {
      display: none; }
    @media(min-width: $md-min) and (max-width: $lg-max) {
      font-size: 0!important;
      min-width: auto!important;
      height: 43px;
      width: 50px;
      padding-left: 0!important;
      padding-right: 0!important;
      position: relative;
      &:before {
        content: "\e960";
        display: block;
        font-family: 'Magicons' !important;
        speak: none;
        font-size: $f13;
        font-style: normal;
        font-weight: normal;
        font-variant: normal;
        text-transform: none;
        line-height: 13px;
        position: absolute;
        top: 50%;
        right: 50%;
        transform: translate(50%, -50%);
        color: #fff;
        transition: $hover; } }
    @media(min-width: $xl-min) {
      width: 170px;
      margin-bottom: 0; } }
  //Links
  &--links {
    margin-bottom: 0;
    overflow-y: auto;
    overflow-x: hidden;
    @media(min-width: $md-min) {
      margin-top: 45px;
      height: calc(100vh - 180px); }
    > li {
      position: relative;
      a {
        display: block;
        padding: 15px 25px;
        color: $text;
        display: flex;
        align-items: center;
        transition: none;
        &:hover,
        &:focus,
        &:active {
          color: $blue;
          i {
            opacity: 1; } }
        i {
          display: block;
          font-size: $f16;
          opacity: 0.5;
          transition: $hover;
          margin-right: 20px;
          transform: translateY(-2px); }
        span {
          display: block;
          @media (min-width: $md-min) and (max-width: $lg-max) {
            opacity: 0;
            transition: $hover;
            transform: translateX(30px); }
          @media(min-width: $xl-min) {
            opacity: 1;
            transform: translateX(0); } }
        .caret {
          font-size: $f10;
          position: absolute;
          top: 25px;
          right: 10px;
          @media (min-width: $md-min) and (max-width: $lg-max) {
            opacity: 0!important; } } }
      .nav-marketing-links,
      .nav-dropdown-links {
        background-color: darken($border, 2%);
        border-top: 1px solid $border;
        border-bottom: 1px solid $border;
        @media (max-width: $sm-max) {
          margin-left: -15px;
          margin-right: -15px; }
        ul {
          list-style: none;
          margin: 0;
          padding: 20px 30px;
          @media (min-width: $md-min) and (max-width: $lg-max) {
            padding: 20px 10px; }
          @media (max-width: $sm-max) {
            padding: 20px 35px; }
          li {
            a {
              padding: 5px 0;
              @media (min-width: $md-min) and (max-width: $lg-max) {
                font-size: 10px;
                line-height: 1.3;
                text-align: center; } }
            &.active {
              a {
                color: $blue; } } } } }
      &.active {
        > a {
          color: $blue;
          font-weight: 500;
          background: $hl-bg-blue;
          i {
            opacity: 1; } } } } }
  .tooltip {
    pointer-events: none;
    margin-right: -150px;
    display: none;
    @media (min-width: $md-min) and (max-width: $lg-max) {
      display: block; } } }

.reputation-nav-toggle {
  background: #fff;
  transition: all .2s ease-in-out;
  position: relative;
  &:before {
    content: "";
    display: block;
    background: #fff;
    width: 4px;
    height: 100%;
    position: absolute;
    margin: auto;
    top: 0;
    left: 0;
    bottom: 0;
    transition: all .2s ease-in-out; }
  &.open {
    background: #e7f3fe;
    &:before {
      background-color: $blue; }
    > a {
      color: $blue;
      i {
        opacity: 1; } } }
  > a {
      position: {}
      .icon {
        font-size: 8px;
        position: absolute;
        top: 50%;
        right: -37px;
        transform: translateY(-50%); } } }
.reputation-nav {
  padding-left: 35px;
  ul {
    padding-bottom: 20px;
    li {
      &.active {
        a {
          background: none; } }
      &:not(:last-child) {
        margin-bottom: 10px; }
      a {
        padding-top: 0;
        padding-bottom: 0; } } } }

