// *////====== MIXINS =====//// *//

@mixin clearfix() {
  &:before,
  &:after {
    content: "";
    display: table; }
  &:after {
    clear: both; } }

@mixin absolute-center() {
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0; }

@mixin cover() {
  width: 100%;
  height: 100%;
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0; }

@mixin center() {
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0; }

@mixin center-align() {
  margin-left: auto;
  margin-right: auto; }

@mixin v-top() {
  position: absolute;
  top: 50%;
  transform: translateY(-50%); }

@mixin v-center() {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%); }

@mixin v-center-parent() {
  -webkit-transform-style: preserve-3d;
  -moz-transform-style: preserve-3d;
  transform-style: preserve-3d; }

@mixin img-center() {
  display: block;
  margin-left: auto;
  margin-right: auto; }

//REM
@function calculateRem($size) {
  $remSize: $size / 16px;
  @return #{$remSize}rem; }

@mixin font-size($size) {
  //font-size: $size //Fallback in px
  font-size: calculateRem($size); }

@mixin rem($property, $size) {
  #{$property}: calculateRem($size); }
