/* ////========== GLOBAL STYLES =========//// */

body {
  font-family: $main-font;
  color: $body-color;
  font-size: $f14;
  font-weight: 400;
  line-height: 1.8;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  @media(min-width: $md-min) {
    padding-top: 82px; }
  //Fullpage
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  > .hl_wrapper {
    flex-grow: 1;
    overflow: hidden; } }

.hl_wrapper {
  position: relative;
  overflow: hidden;
  transition: $basic;
  //padding-top: 60px //temp
  @media(min-width: $md-min) {
    //padding-top: 0
    padding-left: 70px; }
  @media(min-width: $xl-min) {
    padding-left: 250px; }
  &.nav-shrink {
    @media(min-width: $xl-min) {
      padding-left: 70px!important; } }
  //call active
  &.--call-active {
    padding-top: 60px; }
  //Inner
  &--inner {
    padding-top: 25px;
    padding-bottom: 25px; } }

.container-fluid {
  max-width: $container-max;
  @media(min-width: $xl-min) {
    padding-left: 20px;
    padding-right: 20px; } }

a,
button {
  transition: $hover;
  cursor: pointer;
  &:hover,
  &:active,
  &:focus {
    text-decoration: none;
    outline: none; }
  &:active {
    &:focus {
      outline: none; } } }

a[x-apple-data-detectors] {
  color: inherit!important;
  text-decoration: none!important;
  font-size: inherit!important;
  font-family: inherit!important;
  font-weight: inherit!important;
  line-height: inherit!important; }

p {
  margin-bottom: 0;
  + p {
    margin-top: $f15; }
  &.lead {
    font-size: $f18;
    line-height: 1.8;
    font-weight: normal; } }

h1,
h2,
h3,
h4,
h5,
h6 {
  color: $text-drk;
  font-weight: normal;
  margin-top: 0;
  &.heading1 {
    @include rfs(40px); }
  &.heading2 {
    @include rfs(32px); }
  &.heading3 {
    @include rfs(28px); }
  &.heading4 {
    @include rfs(24px); }
  &.heading5 {
    @include rfs(20px); }
  &.heading6 {
    font-size: 16px; } }

hr {
  border-top: 2px solid $hl-bg;
  &.tall {
    margin-top: 30px;
    margin-bottom: 30px; } }

.hl_tooltip {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 16px;
  height: 16px;
  line-height: 16px;
  font-size: $f13;
  border-radius: 50%;
  color: $white;
  font-weight: bold;
  background-color: rgba($hl-text, 0.5);
  &:hover,
  &:focus,
  &:active {
    color: $white;
    background-color: rgba($hl-text, 0.7); } }

.tooltip {
  font-size: $f11!important;
  .tooltip-inner {
    padding: 7px 10px;
    background-color: rgba($hl-text-drk, 0.95); }
  &.bs-tooltip-top {
    .arrow {
      &:before {
        border-top-color: rgba($hl-text-drk, 0.95); } } }
  &.bs-tooltip-bottom {
    .arrow {
      &:before {
        border-bottom-color: rgba($hl-text-drk, 0.95); } } }
  &.bs-tooltip-left {
    .arrow {
      &:before {
        border-left-color: rgba($hl-text-drk, 0.95); } } }
  &.bs-tooltip-right {
    .arrow {
      &:before {
        border-right-color: rgba($hl-text-drk, 0.95); } } } }


* {
  &.--blue {
    color: $blue; }
  &.--yellow {
    color: $yellow; }
  &.--green {
    color: $green; }
  &.--red {
    color: $red; }
  &.--orange {
    color: $orange; }
  &.--purple {
    color: $purple; }
  &.--teal {
    color: $teal; }
  &.--pink {
    color: $pink; }
  &.--gray {
    color: $text; }
  &.--gray-drk {
    color: $text-drk; }
  &.--gray-lt {
    color: $text-lt; } }

.alert {
  border: none;
  color: $white;
  .svg-inline--fa {
    display: inline-block;
    margin-right: 10px; }
  &.--blue {
    background-color: $blue;
    background-color: rgba($blue, 0.1);
    color: $blue; }
  &.--yellow {
    background-color: rgba($yellow, 0.1);
    color: $yellow; }
  &.--green {
    background-color: rgba($green, 0.1);
    color: $green; }
  &.--red {
    background-color: rgba($red, 0.1);
    color: $red; }
  &.--orange {
    background-color: rgba($orange, 0.1);
    color: $orange; }
  &.--purple {
    background-color: $purple;
    background-color: rgba($purple, 0.1);
    color: $purple; }
  &.--teal {
    background-color: rgba($teal, 0.1);
    color: $teal; }
  &.--pink {
    background-color: rgba($pink, 0.1);
    color: $pink; }
  &.--gray {
    background-color: $text;
    background-color: rgba($text, 0.1);
    color: $text; }
  &.--gray-drk {
    background-color: rgba($text-drk, 0.1);
    color: $text-drk; }
  &.--gray-lt {
    background-color: $text-lt; } }


.nav {
  &.nav-tabs {
    border-color: #d5dde1;
    @media (max-width: $sm-max) {
      flex-direction: column;
      justify-content: flex-start; }
    &.--dont-stack {
      @media (max-width: $sm-max) {
        flex-direction: row!important;
        .nav-item {
          &:not(:last-child) {
            margin-right: 20px; } } } }
    .nav-item {
      &:not(:last-child) {
        margin-right: 40px; } }
    .nav-link {
      border: none;
      background: none;
      border-radius: 0;
      padding: 0;
      padding-bottom: 5px;
      border-bottom: 2px solid transparent;
      color: $text;
      @media (max-width: $sm-max) {
        border: none; }
      &:hover,
      &:focus,
      &:active {
        color: $blue;
        background: none; }
      &.active {
        color: $blue;
        border-color: $blue;
        @media (max-width: $sm-max) {
          font-weight: 500; } } }
    &.location-details-tab {
      justify-content: space-between; } } }
.tab-pane {
  padding-top: 30px; }


.list-group {
  .list-group-item {
    border-color: darken($border, 3%);
    transition: $hover;
    white-space: nowrap!important;
    overflow: hidden;
    text-overflow: ellipsis!important;
    &:hover {
      background-color: lighten($border, 2%); }
    p {
      white-space: nowrap!important;
      overflow: hidden;
      text-overflow: ellipsis!important; }
    strong {
      font-weight: normal;
      color: $text-drk; } } }
