/* ////========== CONVERSATIONS STYLES =========//// */

.hl_conversations {
  padding: 0;
  overflow: hidden;
  overflow-x: scroll; }

.hl_conversations--wrap {
  min-width: 950px;
  background: #eee;
  height: 100%;
  display: flex;
  height: calc(100vh - 110px);
  @media(min-width: $md-min) {
    height: calc(100vh - 97px); }
  > * {
    flex: 1 0 0; } }

.hl_conversations--messages-list {
  background-color: #f9fafc;
  max-width: 280px;
  @media(min-width: 1400px) {
    max-width: 320px; }
  .nav-tabs {
    padding-top: 15px;
    padding-left: 15px;
    padding-right: 15px;
    background-color: #f9fafc;
    position: relative;
    z-index: 4; }
  .tab-pane {}
  .tab-content {
    height: 100%;
    .tab-pane {
      padding-top: 0;
      height: 100%; } } }

.messages-list-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  width: 100%;
  padding-bottom: 30px;
  background-color: #f9fafc;
  position: relative;
  z-index: 4;
  h3 {
    font-size: 18px; } }
.messages-list-header-actions {
  display: flex;
  align-items: center;
  > * {
    font-size: $f18;
    &:not(:last-child) {
      margin-right: 15px; } }
  .dropdown {
    .dropdown-toggle {
      &:after {
        display: none; } }
    .dropdown-menu {
      border: none;
      min-width: 360px;
      box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.15);
      .dropdown-menu-filter {
        padding: 25px;
        font-size: $f14;
        .dropdown-menu-filter-header {
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-bottom: 20px;
          h4 {
            margin-bottom: 0;
            font-size: $f18; }
          a {
            font-size: $f12;
            color: $text;
            &:hover,
            &:focus,
            &:active {
              color: $blue; }
            .icon {
              font-size: $f10; } } }
        .messages-filter-item {
          &:not(:last-child) {
            margin-bottom: 20px; }
          h5 {
            font-size: $f12;
            font-weight: normal;
            color: $hl-text; }
          .the-option {
            display: inline-block;
            margin-right: 15px;
            input {
              display: none;
              opacity: 0;
              &:checked {
                + label {
                  color: $blue; } } }
            label {
              font-size: $f14;
              font-weight: 500;
              color: $text-drk;
              margin-bottom: 0;
              cursor: pointer; } } } } } } }
.message-file-uploaded {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  justify-content: flex-start;
  .file-uploaded-img {
    margin-bottom: 5px;
    margin-left: 2px;
    margin-right: 2px;
    min-width: 150px;
    width: 150px;
    height: 110px;
    > img {
      width: 150px;
      height: 110px;
      object-fit: cover;
      object-position: center;
      font-family: 'object-fit: cover; object-position: center;';
      border-radius: 4px;
      cursor: pointer; } } }

.messages-list-search {
  background-color: #f9fafc;
  position: relative;
  z-index: 3;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 20px;
  .icon {
    left: 35px!important;
    transform: translateY(-110%)!important; } }
.messages-list-search,
.files-search {
  margin-bottom: 20px;
  position: relative;
  .form-control {
    background-color: $white;
    border: solid 1px #d0d8e3;
    height: 36px;
    border-radius: 50px;
    padding-left: 40px; }
  .icon {
    position: absolute;
    top: 50%;
    left: 15px;
    transform: translateY(-50%); } }

//messages list
.messages-list {
  margin-left: -20px;
  margin-right: -20px;
  padding-left: 20px;
  padding-right: 20px;
  overflow-y: auto;
  height: 100%;
  margin-top: 0px;
  padding-top: 0px;
  &--item {
    position: relative;
    display: block;
    color: $text;
    padding: 20px 15px 20px 75px;
    transition: $hover;
    cursor: pointer;
    &:hover {
      background-color: #eff1f7; }
    &.active {
      &:before {
        content: "";
        display: block;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background: $orange;
        position: absolute;
        top: 33px;
        left: 7px; }
      h4,
      p,
      .time-date {
        font-weight: 500;
        color: $text-drk; } } }
  &--avatar {
    position: absolute;
    top: 15px;
    left: 20px;
    .icon {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      background-color: #f6f8fb;
      position: absolute;
      top: 50%;
      right: -10px;
      z-index: 2;
      transform: translateY(-50%); } }
  &--text {
    h4 {
      margin-bottom: 0;
      font-size: $f14;
      font-weight: normal; }
    p {
      font-size: $f14; }
    .time-date {
      font-size: $f11;
      color: $text-lt;
      position: absolute;
      top: 20px;
      right: 20px; } } }

.hl_conversations--message {
  background-color: #fff;
  &-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 30px;
    border-bottom: 1px solid #ebeef2;
    h2 {
      font-size: $f18;
      font-weight: 500;
      margin-bottom: 0;
      line-height: 1; }
    .message-header-actions {
      .icon {
        font-size: $f16;
        position: relative;
        top: 1px;
        margin-right: 2px; }
      .dropdown {
        .dropdown-toggle {
          color: $blue; }
        .dropdown-menu {
          box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.15);
          padding-top: 20px;
          padding-bottom: 20px;
          li {
            display: flex;
            align-items: center;
            font-size: $f14;
            padding-left: 30px;
            padding-right: 30px;
            cursor: pointer;
            line-height: 1;
            &:focus,
            &:active {
              color: $text-drk; } } } } }
    .message-header-btns {
      > * {
        display: inline-block;
        width: 36px;
        height: 36px;
        text-align: center;
        border-radius: 50%;
        border: solid 2px $blue;
        font-size: $f16;
        color: $blue;
        position: relative;
        &.call {
          border: solid 2px #00CF76;
          color: $green;
          margin-left: 10px; }
        .icon-video {
          display: block;
          width: 16px;
          height: 12px;
          background: url('../img/icon-video.svg')no-repeat center;
          background-size: 16px 12px;
          @include center(); }
        .icon-call {
          display: block;
          width: 16px;
          height: 16px;
          background: url('../img/icon-call.svg')no-repeat center;
          background-size: 16px;
          @include center(); } } } }
  &-body {
    display: flex;
    height: 100%;
    > * {
      flex: 1 0 0;
      height: 100%; }
    .message-body--conversation {
      padding: 30px;
      padding-bottom: 0;
      position: relative; }
    .message-input-wrap {
      padding-top: 20px;
      position: absolute;
      margin: auto;
      bottom: 40px;
      left: 0px;
      right: 0px;
      padding-left: 30px;
      padding-right: 30px;
      padding-bottom: 20px;
      box-shadow: 0 8px 16px 10px rgba(0, 0, 0, 0.02);
      background: #fff;
      z-index: 3;
      border-bottom-left-radius: 4px;
      border-bottom-right-radius: 4px; }

    .message-box {
      position: relative;
      margin: auto;
      background: #fff;
      z-index: 2;
      padding-bottom: 10px;
      .form-control {
        resize: none;
        &.drag-active {
          background: darken(#f7fafc, 5%); } }
      .message-action-top {
        display: flex;
        align-items: center;
        position: absolute;
        top: 10px;
        right: 10px;
        > * {
          &:not(:last-child) {
            margin-right: 10px; } } }
      .message-action-bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 10px; }
      .btn-blue {
        height: 40px;
        display: flex;
        align-items: center;
        .icon-send {
          display: inline-block;
          width: 14px;
          height: 14px;
          background: url('../img/icon-send-white.svg')no-repeat center;
          background-size: 14px;
          margin-right: 5px;
          position: relative;
          top: -5px; }
        span {
          display: inline-block; } }
      .file-open {
        > input {
          display: none; }
        label {
          cursor: pointer;
          opacity: 0.6;
          transition: $hover;
          margin-bottom: 0;
          &:hover,
          &:active,
          &:focus {
            opacity: 1; } } }
      .emoji-open {
        opacity: 0.6;
        &:hover,
        &:active,
        &:focus {
          opacity: 1; } }
      .use-template {
        display: inline-block;
        font-weight: 500;
        color: #607179;
        opacity: 0.6;
        &:hover,
        &:active,
        &:focus {
          opacity: 1; }
        .icon {
          font-size: 16px; } } }

    .email-box {
      .form-group {
        margin-bottom: 10px; }
      .email-action-bottom {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
        margin-top: 10px;
        > div {
          margin-bottom: 10px; }
        &-right {
          display: flex;
          align-items: center; }
        .file-open {
          > input {
            display: none; }
          label {
            cursor: pointer;
            opacity: 0.6;
            transition: $hover;
            font-weight: 500;
            font-size: $f14;
            &:hover,
            &:active,
            &:focus {
              opacity: 1; } } }
        .btn-blue {
          height: 40px;
          display: flex;
          align-items: center;
          .icon-send {
            display: inline-block;
            width: 14px;
            height: 14px;
            background: url('../img/icon-send-white.svg')no-repeat center;
            background-size: 14px;
            margin-right: 5px;
            position: relative;
            top: -5px; } }
        .use-template {
          display: inline-block;
          font-weight: 500;
          color: #607179;
          opacity: 0.6;
          &:hover,
          &:active,
          &:focus {
            opacity: 1; }
          .icon {
            font-size: 16px; } } } } } }


//Aside
.message-body--aside {
  max-width: 280px;
  padding-bottom: 130px;
  background-color: $white;
  box-shadow: -1px 0 0 0 #ebeef2;
  position: relative;
  display: flex;
  @media (min-width: 1400px) {
    max-width: 300px; }
  .btn-sched-wrap {
    position: absolute;
    margin: auto;
    left: 0;
    right: 0;
    bottom: 50px;
    padding: 10px 30px 30px 30px;
    z-index: 2;
    background: #fff; }
  .btn-sched {
    display: block; }
  .add-tags {
    margin-bottom: 30px;
    .form-control {
      background-color: $white;
      border: solid 1px #d0d8e3;
      height: 36px;
      border-radius: 50px; } } }

.aside-texts {
  text-align: center;
  overflow-y: auto;
  padding: 50px 30px 0 30px;
  width: 100%;
  .avatar {
    height: 72px;
    &_img {
      min-width: 72px;
      width: 72px;
      height: 72px;
      line-height: 72px;
      font-size: $f20;
      > img {
        max-width: 72px;
        max-height: 72px; } }
    @include center-align();
    margin-bottom: 15px; }
  &-name {
    margin-bottom: 40px;
    input {
      font-size: $f18;
      font-weight: 500;
      margin-bottom: 10px;
      max-width: 100%;
      width: auto;
      display: inline-block;
      padding: 0;
      padding-bottom: 5px;
      line-height: 1;
      text-align: center;
      border: none;
      border-bottom: 1px solid #dadee2;
      &:focus,
      &:active {
        outline: none; } }
    h3 {
      font-size: $f18;
      font-weight: 500;
      margin-bottom: 0; }
    p {
      font-size: $f14; } }
  &-appointment {
    display: flex;
    justify-content: space-between;
    margin-bottom: 40px;
    .appointment-item {
      flex: 1 0 0;
      width: 50%;
      &:last-child {
        border-left: 1px solid $border; } }
    h4 {
      font-size: $f12;
      font-weight: normal;
      color: $text;
      margin-bottom: 0; }
    p {
      font-size: $f14;
      color: $text-drk; }
    a {
      font-size: $f14; } }
  &-infos {
    text-align: left;
    margin-bottom: 40px;
    p {
      display: flex;
      align-items: center;
      font-size: $f14;
      color: $text-drk;
      height: 38px;
      i {
        flex: 1 0 0;
        min-width: 16px;
        max-width: 16px;
        margin-right: 15px;
        position: relative;
        top: 4px; }
      + p {
        margin-top: 10px; } }
    input {
      width: 100%;
      padding: 0;
      padding-bottom: 5px;
      border: none;
      border-bottom: 1px solid #dadee2;
      height: 25px;
      position: relative;
      top: 5px;
      &:focus,
      &:active {
        outline: none; } }
    .icon-gender {
      display: inline-block;
      width: 16px;
      height: 16px;
      background: url('../img/icon-gender.svg')no-repeat center;
      background-size: 16px; }
    .icon-phone {
      display: inline-block;
      width: 16px;
      height: 16px;
      background: url('../img/icon-phone.svg')no-repeat center;
      background-size: 16px; }
    .icon-email {
      display: inline-block;
      width: 16px;
      height: 16px;
      background: url('../img/icon-email.svg')no-repeat center;
      background-size: 16px; } }
  &-files {
    text-align: left;
    .files-heading {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 15px;
      h4 {
        margin-bottom: 0;
        font-size: $f14;
        font-weight: 500; }
      a {
        font-size: $f12; } }
    .files-heading-action {
      > * {
        &:not(:last-child) {
          margin-right: 10px; } }
      a {
        color: $text-lt;
        font-size: $f14;
        &:hover,
        &:focus,
        &:active {
          color: $text; }
        &.active {
          color: $blue; } } }
    .no-files {
      text-align: center;
      margin-bottom: 20px;
      .icon {
        font-size: 24px;
        color: $text-lt; }
      p {
        font-size: $f12; } }
    .files-group {
      display: flex;
      align-items: flex-start;
      flex-wrap: wrap;
      margin-left: -5px;
      margin-right: -5px;
      &.--list {
        display: block;
        .file-item {
          width: 100%;
          height: auto;
          background: none!important;
          color: $text-drk;
          text-align: left!important;
          align-items: center;
          justify-content: flex-start;
          font-size: $f14;
          border-radius: 0!important;
          .file-name {
            display: block; }
          .icon {
            &:not(.icon-close) {
              margin-right: 5px; } }
          .file-remove {
            top: 5px!important;
            right: 0!important; }
          &.--doc {
            .icon {
              &:not(.icon-close) {
                color: $blue; } } }
          &.--photo {
            .icon {
              &:not(.icon-close) {
                color: $yellow; } } }
          &.--video {
            .icon {
              &:not(.icon-close) {
                color: #00cf76; } } } } }
      .file-item {
        width: 52px;
        height: 52px;
        border-radius: 8px;
        background-color: $text-lt;
        margin-left: 5px;
        margin-right: 5px;
        margin-bottom: 10px;
        color: $white;
        font-size: $f16;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        &:hover {
          .file-remove {
            opacity: 1; } }
        &.--doc {
          background-color: $blue; }
        &.--photo {
          background-color: $yellow; }
        &.--video {
          background-color: #00cf76; }
        .file-name {
          display: none; }
        .file-remove {
          color: $red;
          font-size: 8px;
          display: flex;
          align-items: center;
          justify-content: center;
          width: 16px;
          height: 16px;
          border-radius: 50%;
          background-color: $white;
          box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
          position: absolute;
          top: -8px;
          right: -8px;
          opacity: 0;
          &:hover {
            opacity: 1; } } } } } }



//the messages
.messages-group {
  //min-height: 605px
  //max-height: calc(100vh - 350px)
  //min-height: 460px
  max-height: calc(100vh - 405px);
  overflow-y: auto;
  margin: -30px;
  padding: 30px;
  transition: $hover;
  &.email-box-active {
    max-height: calc(100vh - 590px); }
  &.drag-active {
    background: darken(#f7fafc, 5%); } }
.messages-group-inner {
  display: flex;
  flex-direction: column; }
.messages-group-heading {
  text-align: center;
  margin-bottom: 20px;
  border-top: 1px solid #e4e7ea;
  img {
    background: #fff;
    padding-left: 10px;
    padding-right: 10px;
    margin-top: -25px; }
  h3 {
    font-size: $f14;
    font-weight: 500;
    margin-bottom: 0; }
  p {
    font-size: $f12;
    color: #8e9eb3; } }
.messages-single {
  position: relative;
  padding-left: 47px;
  margin-bottom: 25px;
  @media (min-width: 1600px) {
    max-width: 80%; }
  .avatar {
    width: 32px;
    height: 32px;
    position: absolute;
    top: 0;
    left: 0;
    .avatar_img {
      min-width: 32px;
      width: 32px;
      height: 32px;
      line-height: 32px;
      font-size: $f10;
      font-weight: 500;
      > img {
        max-width: 32px;
        max-height: 32px; } } }
  .message-bubble {
    border-radius: 0 16px 16px 16px;
    background-color: $blue;
    color: $white;
    padding: 20px 25px;
    margin-bottom: 5px;
    position: relative;
    //
    //white-space: pre-wrap
    &:before {
      content: "";
      display: block;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 0 20px 30px 0;
      border-color: transparent $blue transparent transparent;
      position: absolute;
      top: 0;
      left: -10px; } }
  .time-date {
    color: $text-lt;
    font-size: $f12; }
  a {
    color: white;
    text-decoration: underline; }
  //own
  &.--own-message {
    padding-left: 0;
    padding-right: 47px;
    align-self: flex-end;
    .avatar {
      left: auto;
      right: 0; }
    .message-bubble {
      background-color: #eff2f9;
      border-radius: 16px 0 16px 16px;
      color: $text;
      &:before {
        border-width: 30px 20px 0 0;
        border-color: #eff2f9 transparent transparent transparent;
        left: auto;
        right: -10px; } }
    .time-date {
      float: none;
      text-align: right;
      margin-right: 10px; }
    a {
      color: #607179;
      text-decoration: underline; }
    .message-file-uploaded {
      display: flex;
      justify-content: flex-end;
      align-items: stretch;
      flex-wrap: wrap; } } }


.schedule-appointment-modal {
  .hl_calendar--selection {
    min-width: 790px; }
  .hl_calendar--hours {
    li {
      font-size: $f12;
      height: 35px; } } }


.new-chat-modal {
  .dropdown {
    .dropdown-menu {
      width: 100%;
      border: none;
      box-shadow: 0 8px 16px 5px rgba(0, 0, 0, 0.1);
      .dropdown-item {
        display: flex;
        align-items: center;
        .icon {
          margin-right: 10px; }
        .dropdown-avatar {
          width: 32px;
          height: 32px;
          margin-top: 5px;
          margin-bottom: 5px;
          margin-right: 10px; }
        .dropdown-user {
          h4 {
            font-size: 14px;
            font-weight: 500;
            margin-bottom: 2px;
            line-height: 1; }
          p {
            font-size: 12px;
            color: $text;
            line-height: 1; } } } } } }
