/* ////========== FORM BUILDER STYLES =========//// */

.hl_wrapper {
  &.d-flex {
    align-items: stretch; }
  &.nav-shrink {
    .hl_wrapper--inner {
      &.form-builder {
        .hl_form-builder--main {
          @media (min-width:$xl-min) {
            left: 70px; } } } } } }

.hl_wrapper--inner {
  &.form-builder {
    flex: 1 0 0;
    min-height: 100%;
    padding-right: 300px;
    overflow: hidden; } }

//main
.hl_form-builder--main {
  padding: 40px 20px;
  overflow: auto;
  margin-top: -25px; }

.form-builder--wrap {
  max-width: 550px;
  min-height: 450px;
  margin: 0 auto;
  border-radius: 4px;
  border: 1px dashed darken($border, 10%);
  padding: 30px 15px;
  background-color: $white; }

.form-builder--item {
  margin-bottom: 16px;
  border-radius: 4px;
  border: 2px solid transparent;
  transition: border 0.2s ease-in-out;
  padding: 0 15px;
  position: relative;
  &:hover {
    border: 2px dashed rgba($text-lt, 0.5);
    .form-builder--actions {
      opacity: 1;
      visibility: visible; } }
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin-bottom: 0; }
  label {
    font-size: $f14;
    color: $text-drk; } }

.form-builder--actions {
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
  opacity: 0;
  visibility: hidden;
  transition: $hover;
  z-index: 2; }

.form-builder--move {
  background: #D1E8FE;
  color: $blue;
  display: block;
  width: 25px;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  transition: $hover;
  cursor: grab;
  margin-right: 2px;
  &:hover {
    background: darken(#D1E8FE, 5%); }
  .icon {
    font-size: $f10;
    transform: rotate(45deg); } }

.form-builder--remove {
  background: #FBD9D9;
  color: $red;
  display: block;
  width: 25px;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  transition: $hover;
  cursor: pointer;
  &:hover {
    background: darken(#FBD9D9, 5%); }
  .icon {
    font-size: $f10; } }

//sidebar
.hl_form-builder--sidebar {
  width: 300px;
  background-color: #E7F3FE;
  position: absolute;
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  transition: $hover;
  padding-top: 42px;
  padding-bottom: 85px;
  @media (min-width:$md-min) {
    position: fixed;
    top: 62px; }
  .nav-tabs {
    margin: auto;
    position: absolute;
    top: 40px;
    left: 0;
    right: 0;
    z-index: 2;
    padding-left: 20px;
    padding-right: 20px;
    background-color: {} }
  .tab-content {
    padding: 20px; }
  .custom-field {
    padding: 20px;
    border-top: 1px solid darken(#E7F3FE, 5%);
    position: absolute;
    margin: auto;
    left: 0;
    right: 0;
    bottom: 0; }
  //drag drop
  .dragdrop-wrap {
    position: absolute;
    margin: auto;
    top: 92px;
    left: 0;
    right: 0;
    bottom: 105px;
    overflow-y: auto;
    padding: 0 20px; }
  .dragdrop-items {
    list-style: none;
    margin: 0;
    margin-top: 10px;
    padding: 0;
    li {
      background-color: $white;
      border-radius: 4px;
      padding: 10px 15px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      cursor: all-scroll;
      &:not(:last-child) {
        margin-bottom: 10px; }
      .icon {
        transform: rotate(45deg);
        font-size: $f10; }
      &.active {
        opacity: 0.5;
        .icon {
          display: none; } } } }
  //styles
  .style-wrap {
    position: absolute;
    margin: auto;
    top: 92px;
    left: 0;
    right: 0;
    bottom: 85px;
    overflow-y: auto;
    padding: 0 20px; }
  //option card
  .option-card {
    border-radius: 4px;
    background-color: $white;
    margin-bottom: 10px;
    &-header {
      padding: 10px 15px;
      border-bottom: 2px solid #E7F3FE;
      h3 {
        font-size: $f14;
        margin-bottom: 0; } }
    &-body {
      padding: 15px;
      .bootstrap-select {
        > .btn.dropdown-toggle {
          padding: 10px 20px;
          height: 45px; }
        .filter-option {
          top: 2px; } }
      .form-control {
        &:not(textarea) {
          height: 45px; } }
      .form-group {
        margin-bottom: 10px;
        &:last-child {
          margin-bottom: 0; } } } }
  //style group
  .style-group {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    label {
      font-size: $f14;
      min-width: 100px;
      flex: 1 0 100px;
      margin-bottom: 0; }
    .form-control {
      height: 40px!important; }
    .color-picker {
      position: relative;
      height: 42px;
      border: 1px solid darken($border, 5%);
      border-radius: 4px;
      .form-control {
        padding-right: 45px; }
      .hash {
        position: absolute;
        top: 8px;
        left: 10px;
        color: $text-drk; }
      .the-color {
        display: block;
        width: 40px;
        height: 40px;
        background-color: #ddd;
        background-image: linear-gradient(45deg,#bcbcbc 25%,transparent 25%,transparent 75%,#bcbcbc 75%,#bcbcbc),linear-gradient(45deg,#bcbcbc 25%,transparent 25%,transparent 75%,#bcbcbc 75%,#bcbcbc);
        background-size: 6px 6px;
        background-position: 0 0,3px 3px;
        border-top-right-radius: 4px;
        border-bottom-right-radius: 4px;
        position: absolute;
        top: 0;
        right: 0;
        border-left: 1px solid darken($border, 5%); } }
    .border-picker {
      position: relative;
      height: 42px;
      border: 1px solid darken($border, 5%);
      border-radius: 4px;
      .form-control {
        padding-right: 80px;
        padding-left: 10px; }
      .px {
        position: absolute;
        top: 8px;
        right: 65px; }
      .the-color {
        display: block;
        width: 40px;
        height: 40px;
        background-color: #ddd;
        background-image: linear-gradient(45deg,#bcbcbc 25%,transparent 25%,transparent 75%,#bcbcbc 75%,#bcbcbc),linear-gradient(45deg,#bcbcbc 25%,transparent 25%,transparent 75%,#bcbcbc 75%,#bcbcbc);
        background-size: 6px 6px;
        background-position: 0 0,3px 3px;
        border-top-right-radius: 4px;
        border-bottom-right-radius: 4px;
        position: absolute;
        top: 0;
        right: 0; }
      .pixel-count-btns {
        position: absolute;
        top: 0;
        right: 40px;
        display: flex;
        flex-direction: column;
        button {
          width: 20px;
          height: 22px;
          border: 1px solid darken($border, 5%);
          //background-color: $white
          border-radius: 0!important;
          display: flex;
          align-content: center;
          justify-content: center;
          color: $text;
          font-size: 8px;
          margin: 0;
          margin-top: -1px;
          &.pixel-up {}
          &.pixel-down {
            margin-top: -2px; }
          &:hover {
            background-color: $white;
            color: $blue; } } } }
    .pixel-count {
      position: relative;
      height: 42px;
      border: 1px solid darken($border, 3%);
      border-radius: 4px;
      .form-control {
        padding-right: 45px;
        padding-left: 10px; }
      .px {
        position: absolute;
        top: 8px;
        right: 25px; }
      .pixel-count-btns {
        position: absolute;
        top: 0;
        right: -1px;
        display: flex;
        flex-direction: column;
        button {
          width: 20px;
          height: 22px;
          border: 1px solid darken($border, 5%);
          //background-color: $white
          border-radius: 0!important;
          display: flex;
          align-content: center;
          justify-content: center;
          color: $text;
          font-size: 8px;
          margin: 0;
          margin-top: -1px;
          &.pixel-up {
            border-top-right-radius: 4px!important; }
          &.pixel-down {
            margin-top: -2px;
            border-bottom-right-radius: 4px!important; }
          &:hover {
            background-color: $white;
            color: $blue; } } } } } }

.hl_add-custom-field {
  .tab-pane {
    padding-top: 0;
    h3 {
      font-size: $f18;
      margin-bottom: 20px; } } }
