/* ////========== CONTROLS STYLES =========//// */

.hl_controls {
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-between;
  > * {
    &:not(:last-child) {
      margin-right: 20px; } }
  .hl_controls--left {
    &.flex {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      > * {
        margin-bottom: 10px;
        &:not(:last-child) {
          margin-right: 15px; } } }
    h1,
    h2,
    h3 {
      font-size: $f20;
      margin: 0;
      span {
        font-size: $f14;
        color: $text-lt;
        display: inline-block;
        margin-left: 10px; }
      .back {
        display: inline-block;
        margin-right: 10px;
        font-size: $f14; } }
    .dropdown {
      .btn {
        &.btn-xs {
          padding: 10px 40px 10px 15px!important;
          height: auto!important;
          font-size: $f14;
          &:after {
            font-size: $f10;
            right: 15px; } } } }
    > .bootstrap-select {
      margin-right: 20px;
      &:not(.with-btn) {
        > .btn.dropdown-toggle {
          height: 40px;
          padding-left: 0;
          padding-right: 25px!important;
          background: none!important;
          &:after {
            right: 0;
            color: $blue; }
          .filter-option {
            font-size: $f20;
            font-weight: 400;
            color: $text-drk;
            margin-top: -10px; } } }
      &.with-btn {
        > .btn.dropdown-toggle {
          background-color: #fff;
          padding: 9.5px 50px 9.5px 20px;
          height: 40px!important;
          .filter-option {
            font-weight: normal;
            color: $text-drk; } } } }
    .total {
      display: inline-block;
      color: $hl-text-lt; }
    .status {
      display: inline-block;
      background-color: rgba($text, 0.15);
      border-radius: 100px;
      padding: 8px 20px;
      line-height: 1;
      color: $text;
      &.--published {
        background-color: $green;
        color: #fff; } }
    .btn:not(.btn-sm) {
      font-weight: normal;
      padding: 9.5px 20px;
      color: $text-drk;
      &:after {
        color: $blue!important; } }
    .triggers-results {
      display: flex;
      align-items: center;
      .bootstrap-select {
        margin-left: 8px;
        margin-right: 8px;
        width: auto!important;
        > .btn.dropdown-toggle {
          padding: 0!important;
          height: auto;
          border-radius: 0;
          min-width: auto!important;
          background: none!important;
          &:after {
            display: none;
            visibility: hidden;
            opacity: 0; }
          .filter-option {
            display: inline-block;
            font-weight: 500;
            color: $text-drk;
            line-height: 1.2;
            border-bottom: 2px solid $text-drk;
            position: relative;
            top: 2px; } } } } }
  .hl_controls--right {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    > * {
      margin-bottom: 10px;
      &:not(:last-child) {
        margin-right: 10px; } }
    .bootstrap-select {
      > .btn.dropdown-toggle {
        background-color: #fff;
        padding: 9.5px 50px 9.5px 20px;
        height: 40px!important;
        &:after {
          color: $blue; }
        .filter-option {
          font-weight: normal;
          color: $text-drk; } } }
    .search-form {
      position: relative;
      @media (min-width:$lg-min) {
        min-width: 300px; }
      .icon {
        position: absolute;
        top: 50%;
        left: 20px;
        transform: translateY(-50%);
        color: rgba($hl-text, 0.5); }
      input {
        padding: 10px 20px 10px 50px;
        height: 40px; } }
    .hl_reviews--filter-btn {
      &:focus {
        background: #fff;
        color: $text; }
      &.--open {
        color: $blue;
        background: rgba($hl-blue, 0.1); } }
    .btn:not(.btn-sm) {
      font-weight: bold;
      padding: 9.5px 20px;
      .icon {
        margin-right: 10px; } }
    .btn-sm {
      display: inline-flex;
      align-items: center;
      > span {
        + .icon {
          margin-left: 10px; } }
      .icon {
        display: block;
        width: 16px;
        color: $blue;
        font-size: $f16; } }
    .form-group {
      display: flex;
      align-items: center;
      margin-bottom: 10px;
      margin-right: 20px;
      label {
        margin-bottom: 0;
        margin-right: 10px;
        font-size: 14px; }
      &.form-date {
        .icon {
          bottom: 13px; } } }
    .form-control {
      height: 40px; } } }
