/* ////========== BUTTONS STYLES =========//// */

.btn {
  font-family: $main-font;
  font-size: $f14;
  font-weight: 500;
  color: #fff;
  padding: 11px 20px;
  transition: $hover;
  min-width: 85px;
  border-radius: $border-radius;
  border: none;
  &:hover,
  &:focus,
  &:active {
    color: #fff;
    outline: none;
    box-shadow: none; }
  &:active {
    &:focus {
      outline: none;
      box-shadow: none!important; } }
  &.dropdown-toggle {
    padding-right: 45px!important;
    //Caret
    &:after {
      content: "\e904";
      display: block;
      margin: 0;
      position: absolute;
      top: 50%;
      right: 20px;
      transform: translateY(-50%);
      font-family: 'Magicons' !important;
      speak: none;
      font-style: normal;
      font-weight: normal;
      font-variant: normal;
      text-transform: none;
      line-height: 1;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
      width: 11px;
      height: 10px;
      font-size: 10px;
      border: none!important; }
    &:focus {
      outline: none;
      box-shadow: none!important; } }
  &:active {
    transform: translateY(2px); }
  //Size
  &.btn-sm {
    padding: 9.5px 15px;
    height: 40px;
    font-size: $f14;
    font-weight: normal;
    min-width: 0; }
  &.btn-xs {
    padding: 7px 11px;
    height: 28px;
    font-size: $f11;
    font-weight: 500;
    min-width: 0; }
  &.btn-xss {
    padding: 5px 10px;
    height: 25px;
    font-size: $f10;
    font-weight: 500;
    min-width: 0; }
  &.btn-slim {
    padding: 5px 10px;
    height: 25px;
    font-size: $f10;
    text-transform: uppercase;
    font-weight: 500;
    min-width: 150px; }
  //Types
  &-square {
    width: 45px;
    height: 45px;
    line-height: 47px;
    padding: 0;
    text-align: center;
    min-width: auto;
    font-size: $f14; }
  &-square-sm {
    width: 35px;
    height: 35px;
    line-height: 35px;
    padding: 0;
    text-align: center;
    min-width: auto;
    font-size: $f14; }
  &-circle {
    width: 45px;
    height: 45px;
    line-height: 47px;
    padding: 0;
    text-align: center;
    border-radius: 50px;
    min-width: auto;
    font-size: $f16; }
  &-circle-sm {
    width: 35px;
    height: 35px;
    line-height: 35px;
    padding: 0;
    text-align: center;
    border-radius: 50px;
    min-width: auto;
    font-size: $f14; }
  //Colors
  &.btn-link {
    color: $blue;
    padding-left: 0;
    padding-right: 0;
    &:hover,
    &:focus,
    &:active {
      text-decoration: none; } }
  &.btn-link2 {
    color: $text;
    padding-left: 0;
    padding-right: 0;
    &:hover,
    &:focus,
    &:active {
      color: $blue;
      text-decoration: none; } }
  &.btn-yellow {
    background-color: rgba($yellow, 0.1);
    border-color: rgba($yellow, 0.1);
    color: $yellow;
    &:hover,
    &:focus,
    &:active {
      color: $yellow;
      background-color: rgba($yellow, 0.2);
      border-color: rgba($yellow, 0.2); } }
  &.btn-primary {
    background-color: rgba($blue, 0.1);
    border-color: rgba($blue, 0.1);
    color: $blue;
    &:hover,
    &:focus,
    &:active {
      color: $blue!important;
      background-color: rgba($blue, 0.2)!important;
      border-color: rgba($blue, 0.2)!important; } }
  &.btn-blue {
    background-color: $blue;
    border-color: $blue;
    color: #fff;
    &:hover,
    &:focus,
    &:active {
      color: #fff;
      background-color: $blue;
      border-color: $blue; } }
  &.btn-light {
    background: #fff;
    border-color: $border;
    color: $text;
    &:hover,
    &:focus,
    &:active {
      background: rgba($hl-blue, 0.1);
      color: $blue; } }
  &.btn-light2 {
    background: #f7fafc;
    border-color: #f7fafc;
    color: $text;
    &:hover,
    &:focus,
    &:active {
      background: darken(#f7fafc, 5%);
      color: $text; } }
  &.btn-light3 {
    background: #fff;
    border: 1px solid darken($border, 5%);
    color: $text;
    &:hover,
    &:focus,
    &:active {
      background: $border;
      color: $text; } }
  &.btn-light4 {
    background: darken(#f7fafc, 2%);
    border-color: darken(#f7fafc, 2%);
    color: $text;
    &:hover,
    &:focus,
    &:active {
      background: darken(#f7fafc, 5%);
      color: $text; } }
  &.btn-light5 {
    background: #fff;
    border: none!important;
    color: $blue;
    font-weight: bold;
    &:hover,
    &:focus,
    &:active {
      background: $hl-blue;
      color: $white; } }
  &.btn-light6 {
    background: #fff;
    border: none!important;
    color: $orange;
    font-weight: bold;
    &:hover,
    &:focus,
    &:active {
      background: $hl-orange;
      color: $white; } }
  &.btn-green-lt {
    background-color: rgba($green, 0.1);
    border-color: rgba($green, 0.1);
    color: $green;
    &:hover,
    &:focus,
    &:active {
      color: $green;
      background-color: rgba($green, 0.2);
      border-color: rgba($green, 0.2); } }
  &.btn-orange {
    background: $orange;
    border-color: $orange;
    color: $white;
    &:hover,
    &:focus,
    &:active {
      background: darken($orange, 10%);
      color: $white; } } }


