/* ////========== CALL STYLES =========//// */

.hl_call-bar {
  background-color: $white;
  //border-top: 3px solid rgba($border, 0.3)
  border-left: 2px solid $hl-bg;
  display: flex;
  height: 82px;
  margin-left: 20px;
  margin-right: -30px;
  &.--in {
    //border-top: 3px solid rgba($green, 0.5)
    .call-type {
      color: $green;
      .icon {
        transform: rotate(135deg); } } }
  &.--out {
    //border-top: 3px solid rgba($teal, 0.5)
    .call-type {
      color: $teal;
      .icon {
        transform: rotate(45deg); } } }

  .call-type {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 5px 20px;
    width: 60px;
    min-width: 60px;
    .icon {
      display: block;
      font-size: $f16; }
    p {
      text-transform: uppercase;
      font-weight: 500;
      font-size: $f14; } }

  .hl_call-bar--inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 4.5px 10px 4.5px 20px;
    border-left: 2px solid $border;
    border-right: 2px solid $border;
    flex: 1 0 0;
    .caller-id {
      min-width: 150px;
      h4 {
        font-size: $f16;
        margin-bottom: 0; }
      p {
        font-size: $f12; } }
    .call-buttons {
      display: flex;
      align-items: center;
      .btn {
        font-size: $f16;
        color: $text;
        &:hover,
        &:focus,
        &:active {
          background: $border;
          color: $text; }
        &.btn-arrows {
          transform: rotate(45deg); } } }
    .call-dial {
      .dropdown-toggle {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: $f20;
        color: $text;
        width: 45px;
        height: 45px;
        line-height: 45px;
        padding: 0;
        text-align: center;
        border-radius: 50px;
        &:hover,
        &:focus,
        &:active {
          background: $border;
          color: $text; } }
      .dropdown-menu {
        min-width: 250px;
        max-width: 250px;
        padding: 20px;
        margin-top: 10px;
        .dial-numbers {
          display: flex;
          flex-wrap: wrap;
          justify-content: center;
          .dial-item {
            display: block;
            flex: 1 0 0;
            min-width: 33.3333%;
            width: 33.3333%;
            text-align: center;
            margin-bottom: 10px;
            .number {
              display: flex;
              align-items: center;
              justify-content: center;
              width: 50px;
              height: 50px;
              margin: 0 auto;
              border: 1px solid darken($border, 5%);
              border-radius: 50%;
              color: $text-drk;
              &:hover {
                background-color: $border; } }
            .letters {
              text-transform: uppercase;
              font-size: $f13;
              color: $text-lt; } } } } } }
  .call-actions {
    padding: 4.5px 20px;
    display: flex;
    align-items: center;
    > * {
      &:not(:last-child) {
        margin-right: 15px; } }
    .call-time {
      color: $text-drk; }
    .btn-call {
      .fa-phone {
        transform: rotate(-135deg); } }
    .btn-next-lead {
      font-size: $f12;
      height: 30px;
      border-radius: 40px;
      .icon {
        display: inline-block;
        margin-left: 5px; } } } }

