/* ////========== DASHBOARD STYLES =========//// */

//Reviews
.hl_dashboard--reviews {
  .card-body {
    padding: 30px;
    @media (min-width: $sm-min) {
      padding: 40px 50px; } }
  h3 {
    font-size: $f50;
    font-weight: 300;
    line-height: 1;
    margin-bottom: 20px; }
  //Stats
  &-stats {
    > div {
      margin-bottom: 20px; }
    h4 {
      font-weight: 400;
      font-size: $f22;
      .icon {
        font-size: $f16;
        color: $green; }
      .icon-positive {
        margin-right: 10px; } }
    p {
      max-width: 200px; } }
  //Sources
  &-sources {
    padding-top: 20px;
    h4 {
      font-weight: 400;
      font-size: $f14;
      color: $hl-text;
      margin-bottom: 15px; }
    .sources_list {
      li {
        a {
          display: block;
          width: 35px;
          height: 35px;
          border-radius: 50%;
          background-color: #eee;
          background-repeat: no-repeat;
          background-position: center;
          background-size: 35px;
          text-indent: -999999px;
          &.google {
            background-image: url('../img/icon-g+.svg'); }
          &.facebook {
            background-image: url('../img/icon-facebook.svg'); }
          &.twitter {
            background-image: url('../img/icon-twitter.svg'); }
          &.add {
            background-image: url('../img/icon-add.svg'); } } } } } }





//Avg. Rating
.hl_dashboard--avg-rating {
  .card-body {
    padding: 30px;
    @media (min-width: $sm-min) {
      padding: 40px 50px; } }
  h3 {
    font-size: $f50;
    font-weight: 300;
    line-height: 1;
    margin-bottom: 20px;
    vertical-align: top;
    .rating {
      display: inline-block;
      position: relative;
      top: 5px;
      .icon {
        vertical-align: top; } } }
  //Rating Percentage
  .rating_percentage {
    margin-bottom: 40px;
    display: flex;
    align-items: center;
    h4 {
      font-size: $f22;
      color: $hl-text-lt;
      font-weight: 400;
      margin-bottom: 0;
      margin-right: 20px;
      position: relative;
      // &:before
      //   content: ""
      //   display: block
      //   width: 16px
      //   height: 2px
      //   background-color: $hl-text-lt
      //   border-radius: 4px
      //   position: absolute
      //   top: 8px
 }      //   left: 0
    p {
      max-width: 200px; } }
  //Rating stats
  .rating_stats {
    &-item {
      margin-bottom: 15px;
      display: flex;
      h4 {
        font-size: $f14;
        margin-bottom: 0;
        margin-right: 20px;
        width: 40px;
        .icon {
          color: $hl-text-lt;
          font-size: $f10; } }
      .progress {
        width: 100%;
        border-radius: 0;
        height: 15px;
        align-items: center;
        background-color: transparent;
        .progress-bar {
          text-align: right;
          height: 4px;
          border-radius: 10px; }
        span {
          position: relative;
          right: -10px;
          color: $text-drk; } }
      &.--star5 {
        .progress {
          .progress-bar {
            background-color: $green; } } }
      &.--star4 {
        .progress {
          .progress-bar {
            background-color: $blue; } } }
      &.--star4 {
        .progress {
          .progress-bar {
            background-color: $yellow; } } }
      &.--star2 {
        .progress {
          .progress-bar {
            background-color: $orange; } } }
      &.--star1 {
        .progress {
          .progress-bar {
            background-color: $red; } } } } } }





//Sentiment
.hl_dashboard--sentiment {
  .card-body {
    padding-top: 40px;
    padding-bottom: 40px;
    display: flex;
    flex-direction: column;
    justify-content: center; }
  hr {
    max-width: 100px;
    margin-top: 30px;
    margin-bottom: 30px; } }
.sentiment_review {
  text-align: center;
  &-inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 200px;
    @include center-align(); }
  h3 {
    font-size: 32px;
    font-weight: 300; }
  h4 {
    font-size: 22px; }
  &-graph {
    width: 50px;
    height: 50px;
    position: relative;
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 20px;
    .percentage {
      display: block;
      width: 50px;
      background: rgba(#fff, 0.5);
      position: absolute;
      margin: auto;
      top: 0;
      left: 0;
      right: 0; } }
  //Positive
  &.--positive {
    h4 {
      color: $hl-green; }
    .sentiment_review-graph {
      background: url('../img/icon-positive.png')no-repeat center;
      background-size: 50px; } }
  //Negative
  &.--negative {
    h4 {
      color: $hl-red; }
    .sentiment_review-graph {
      background: url('../img/icon-negative.png')no-repeat center;
      background-size: 50px; } } }





//Leaderboard Invites
.hl_dashboard--leaderboard-invites {
  .card-body {
    padding-top: 30px;
    padding-bottom: 30px; } }
.leaderboard_item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  + .leaderboard_item {
    margin-top: 20px; }
  .avatar {
    margin-right: 10px; }
  > p {
    color: #90a4ae;
    font-size: $f14;
    line-height: 1;
    strong {
      font-size: $f18;
      font-weight: normal;
      color: $text-drk; } } }





//Invites Goal
.hl_dashboard--invites-goal {
  .card-body {
    padding-top: 40px;
    padding-bottom: 40px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; } }
.invites-goal_progress {
  width: 100%;
  max-width: 250px;
  text-align: center;
  h3 {
    font-size: $f55;
    font-weight: 300;
    position: relative;
    &:before {
      content: "";
      display: block;
      width: 16px;
      height: 14px;
      background: url('../img/icon-send.svg')no-repeat center;
      background-size: 16px 14px;
      position: absolute;
      margin: auto;
      top: -36px;
      left: 0;
      right: 0; } }
  hr {
    max-width: 110px;
    margin-bottom: 30px; }
  h4 {
    font-size: $f16;
    margin-bottom: 5px; } }





//Latest Review Requests
.hl_dashboard--latest-review-requests {
  .table {
    tbody {
      tr {
        td {
          &:last-child {
            @media (min-width: $lg-min) {
              width: 150px; }
            .btn-blue {
              margin-right: 10px; } } } } } } }




//Email Sent
.dashboard_emails-sent {
  //chart
  &-chart {
    .highcharts-credits {
      display: none!important; }
    //stats
    &-stats {
      margin-top: 20px;
      margin-bottom: 20px;
      margin-left: 10px;
      .list-inline-item {
        margin-bottom: 20px;
        &:first-child {
          p {
            padding-left: 0;
            &:before {
              display: none; } } }
        &:nth-child(2) {
          p {
            &:before {
              background-color: $hl-yellow; } } }
        &:nth-child(3) {
          p {
            &:before {
              background-color: $hl-green; } } }
        &:nth-child(4) {
          p {
            &:before {
              background-color: $hl-blue; } } }
        &:nth-child(5) {
          p {
            &:before {
              background-color: $hl-red; } } } }
      .list-inline-item:not(:last-child) {
        margin-right: 20px;
        @media (min-width: $sm-min) {
          margin-right: 50px; } }
      h4 {
        margin-bottom: 5px;
        @include rfs(34px);
        font-weight: 300; }
      p {
        position: relative;
        padding-left: 25px;
        &:before {
          content: "";
          display: block;
          width: 15px;
          height: 4px;
          background-color: #eee;
          border-radius: 4px;
          position: absolute;
          top: 50%;
          left: 0;
          transform: translateY(-50%); } } } } }


//Latest Website Visitor
.hl_dashboard--website-visitors {
  .card-body {
    padding-top: 50px;
    padding-bottom: 50px; }
  .row {
    @media (min-width: $xl-min) {
      margin-left: -30px;
      margin-right: -30px;
      > div {
        padding-left: 30px;
        padding-right: 30px;
        &:first-child {
          border-right: 1px solid #ebeef2; } } } }
  h3 {
    color: $text;
    text-transform: uppercase;
    font-size: $f14;
    margin-bottom: 20px;
    @media (min-width: $xl-min) {
      margin-bottom: 40px; } }
  .website-visitors--set1 {
    margin-bottom: 40px;
    > div {
      margin-bottom: 50px; } }
  .website-visitors--set2 {
    margin-bottom: 40px;
    > div {
      margin-bottom: 50px; } }
  .website-visitors--set3 {
    > div {
      margin-bottom: 50px;
      @media (min-width: $md-min) {
        margin-bottom: 0; } } }
  //coverstion rate
  .conversion-rate {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    max-width: 430px;
    margin: 0 auto;
    @media (min-width: $sm-min) {
      flex-direction: row; }
    .total-visitor,
    .appointments {
      h4 {
        margin-bottom: 0;
        font-size: $f18;
        font-weight: 500;
        .icon {
          margin-right: 5px; } }
      p {
        font-size: $f14; } }
    .rate {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 150px;
      height: 150px;
      border-radius: 50%;
      color: $white;
      text-align: center;
      margin: 20px 0;
      @media (min-width: $sm-min) {
        margin: 0; }
      &.--blue {
        background-color: $blue; }
      &.--yellow {
        background-color: $yellow; }
      &.--green {
        background-color: $green; }
      &.--red {
        background-color: $red; }
      &.--orange {
        background-color: $orange; }
      &.--purple {
        background-color: $purple; }
      &.--teal {
        background-color: $teal; }
      &.--pink {
        background-color: $pink; }
      h4 {
        color: $white;
        margin-bottom: 0;
        font-size: $f50;
        font-weight: 300;
        line-height: 1;
        span {
          font-size: $f18;
          font-weight: bold;
          vertical-align: top;
          position: relative;
          top: 6px;
          left: -4px; } } } }
  //device stats
  .device_stats {
    &-item {
      margin-bottom: 30px;
      display: flex;
      h4 {
        font-size: $f14;
        margin-bottom: 0;
        margin-right: 5px;
        width: 100px;
        display: flex;
        align-items: center;
        .icon {
          margin-right: 10px; } }
      .progress {
        width: 100%;
        border-radius: 0;
        height: 15px;
        align-items: center;
        background-color: transparent;
        .progress-bar {
          text-align: right;
          height: 4px;
          border-radius: 10px;
          margin-right: 10px; }
        span {
          position: relative;
          right: 0;
          color: $text-drk;
          font-size: $f14; } }
      &.--green {
        .progress {
          .progress-bar {
            background-color: $green; } } }
      &.--blue {
        .progress {
          .progress-bar {
            background-color: $blue; } } }
      &.--yellow {
        .progress {
          .progress-bar {
            background-color: $yellow; } } } } }
  //visitors
  .new-visitors,
  .returning-visitors, {
    text-align: center;
    position: relative;
    padding-top: 15px;
    &:before {
      content: "";
      display: block;
      width: 24px;
      height: 4px;
      border-radius: 2px;
      background-color: $text;
      position: absolute;
      margin: auto;
      top: 0;
      left: 0;
      right: 0; }
    h4 {
      font-size: $f18;
      margin-bottom: 0;
      font-weight: bold; } }
  .new-visitors {
    &:before {
      background-color: $blue; } }
  .returning-visitors, {
    &:before {
      background-color: $yellow; } }
  .visitor-count-donut {
    position: relative;
    width: 100%;
    height: 200px;
    margin: 0 auto;
    h4 {
      //display: flex
      //flex-direction: column
      //justify-content: center
      //align-items: center
      //width: auto
      //height: 200px
      font-size: $f30;
      line-height: 1;
      margin-bottom: 0;
      text-align: center;
      font-weight: 500;
      position: absolute;
      margin: auto;
      top: 50%;
      left: 0;
      right: 0;
      transform: translateY(-50%);
      span {
        display: block;
        font-size: $f14;
        color: $hl-text; } }
    .visitor-donut-chart {
      width: 200px;
      height: 200px;
      margin: 0 auto;
      .highcharts-credits {
        display: none!important; } } }

  //age stats
  .age_stats {
    &:last-child {
      .age_stats-item {
        margin-bottom: 0; } }
    &-item {
      margin-bottom: 25px;
      display: flex;
      h4 {
        font-size: $f14;
        margin-bottom: 0;
        margin-right: 5px;
        width: 60px;
        .icon {
          color: $hl-text-lt;
          font-size: $f10; } }
      .progress {
        width: 100%;
        border-radius: 0;
        height: 15px;
        align-items: center;
        background-color: transparent;
        .progress-bar {
          text-align: right;
          height: 4px;
          border-radius: 10px;
          margin-right: 10px; }
        span {
          position: relative;
          right: 0;
          color: $text-drk;
          font-size: $f14; } }
      &.--green {
        .progress {
          .progress-bar {
            background-color: $green; } } }
      &.--blue {
        .progress {
          .progress-bar {
            background-color: $blue; } } }
      &.--yellow {
        .progress {
          .progress-bar {
            background-color: $yellow; } } }
      &.--orange {
        .progress {
          .progress-bar {
            background-color: $orange; } } }
      &.--red {
        .progress {
          .progress-bar {
            background-color: $red; } } }
      &.--purple {
        .progress {
          .progress-bar {
            background-color: $purple; } } } } }
  //visitors gender
  .visitors-gender {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .female-icon {
      display: flex;
      justify-content: center;
      align-items: center;
      min-width: 64px;
      width: 64px;
      height: 64px;
      border-radius: 50%;
      background-color: $pink;
      @media (max-width: 480px) {
        min-width: 45px;
        width: 45px;
        height: 45px;
        .icon {
          transform: scale(0.6); } } }
    .male-icon {
      display: flex;
      justify-content: center;
      align-items: center;
      min-width: 64px;
      width: 64px;
      height: 64px;
      border-radius: 50%;
      background-color: $blue;
      @media (max-width: 480px) {
        min-width: 45px;
        width: 45px;
        height: 45px;
        .icon {
          transform: scale(0.6); } } }
    .gender-percentages {
      width: 100%;
      margin: 20px 20px;
      @media (min-width: $sm-min) {
        margin: 0 20px; }
      .gender-percentages-numbers {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 15px;
        .gender-percentages-numbers-item {
          h4 {
            font-size: $f18;
            font-weight: 500;
            margin-bottom: 0; } } }
      .gender-percentages-progress {
        display: flex;
        align-items: center;
        .progress {
          margin: 0 2px;
          .progress-bar {
            width: 100%; } } } } }
  .visitors-gender-chart {
    margin-top: 50px;
    .visitors-gender-chart {
      height: 200px; }
    .highcharts-credits {
      display: none!important; } } }


.hl_dashboard--matters-added,
.hl_dashboard--pipeline-value,
.hl_dashboard--conversion-rate {
  .card-body {
    text-align: center; }
  h4 {
    font-size: $f14;
    text-transform: uppercase;
    color: $text-lt; }
  h3 {
    font-size: $f45;
    font-weight: 300;
    line-height: 1;
    margin-bottom: 30px; }
  .bottom-stats {
    list-style: none;
    margin: 0;
    padding: 0;
    li {
      display: flex;
      justify-content: space-between;
      font-size: $f14;
      strong {
        font-weight: normal;
        color: $text-drk; }
      &:nth-child(1) {
        span {
          color: $green; } }
      &:nth-child(2) {
        span {
          color: $yellow; } }
      &:nth-child(3) {
        span {
          color: $red; } } } } }

.hl_dashboard--sales-funnel {
  .sales-funnel-chart {
    width: 500px;
    @include center-align();
    .highcharts-credits {
      display: none!important; } } }


.hl_dashboard--return-on-investment {
  .row {
    @media (min-width: $lg-min) {
      align-items: center; } } }

.dashboard_return-on-investment-chart-stats-top {
  display: flex;
  align-items: center;
  padding-bottom: 30px;
  margin-bottom: 30px;
  border-bottom: 1px solid #ebeef2;
  @media (max-width: $md-max) {
    max-width: 400px;
    margin-top: 40px;
    margin-left: auto;
    margin-right: auto; }
  > div {
    flex: 1 0 0;
    width: 50%; }
  .stat-item {
    &:not(:last-child) {
      margin-bottom: 20px; }
    h4 {
      font-size: $f18;
      font-weight: 500;
      margin-bottom: 0;
      position: relative;
      &:before {
        content: "";
        display: inline-block;
        width: 14px;
        height: 14px;
        border-radius: 50%;
        border: 4px solid #eee;
        margin-right: 10px; } }
    &.--revenue {
      h4 {
        &:before {
          border-color: $blue; } } }
    &.--cost {
      h4 {
        &:before {
          border-color: $yellow; } } } }
  .stat-percentage {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 120px;
    height: 120px;
    border-radius: 50%;
    background-color: #00d26d;
    color: $white;
    margin-left: auto;
    margin-right: auto;
    h4 {
      color: $white;
      margin-bottom: 0;
      font-size: 30px;
      font-weight: bold;
      vertical-align: top;
      line-height: 1;
      span {
        display: inline-block;
        font-size: 14px;
        vertical-align: top; } }
    p {
      line-height: 1; } } }

.dashboard_return-on-investment-chart-stats-bottom {
  display: flex;
  align-items: flex-start;
  margin-bottom: 20px;
  @media (max-width: $md-max) {
    max-width: 400px;
    margin-left: auto;
    margin-right: auto; }
  > div {
    flex: 1 0 0;
    width: 50%; }
  .stat-item {
    text-align: center;
    padding-bottom: 20px;
    border-bottom: 2px solid #ebeef2;
    &:first-child {
      border-right: 2px solid #ebeef2; }
    h4 {
      margin-bottom: 0;
      font-size: 14px;
      font-weight: 500; }
    p {
      font-size: 12px; } } }

.cost-aapointment {
  text-align: center;
  h4 {
    font-size: 24px;
    font-weight: 500;
    margin-bottom: 0; } }
