/* ////========== TRIGGERS STYLES =========//// */

.hl_triggers--table {
  .table {
    tbody {
      tr {
        td {
          h4 {
            margin-bottom: 0;
            font-size: $f14;
            font-weight: 500; }
          &:nth-child(2),
          &:nth-child(3) {
            width: 100px; } } } } } }

.hl_triggers--wrap {
  padding-top: 20px; }

.hl_triggers--control {
  margin-bottom: 20px;
  .dropdown {
    display: inline-block;
    &.show {
      .dropdown-toggle {
        background-color: $white; } }
    .dropdown-toggle {
      border-radius: 100px;
      &:hover,
      &:focus,
      &:active {
        background-color: $white;
        color: $text; }

      strong {
        font-weight: 500;
        color: $text-drk; } }
    .dropdown-menu {
      width: 300px;
      font-size: $f14;
      padding: 20px;
      p {
        + p {
          margin-top: 10px; }
        > a {
          &:not(.btn) {
            display: block;
            color: $text; }
          &:hover {
            color: $blue; }
          &.active {
            color: $text-drk;
            font-weight: 500; } } } } } }


.hl_triggers--items {
  list-style: none;
  padding: 0;
  margin: 0; }
.hl_triggers--item {
  margin-bottom: 10px;
  background: $white;
  border-radius: 4px;
  padding: 10px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  .triggers_name {
    h4 {
      margin-bottom: 0;
      font-size: $f14;
      font-weight: 500;
      transition: $hover;
      &:hover {
        color: $blue; } } }
  .triggers_actions {
    > .dropdown {
      margin-right: 10px; }
    .dropdown {
      display: inline-block;
      .dropdown-toggle {
        padding: 7px 14px;
        padding-right: 30px!important;
        min-width: 0;
        font-size: $f13;
        &:after {
          font-size: $f8;
          right: 10px; } } }
    .btn-group {
      .btn {
        padding: 7px 14px;
        min-width: 0;
        font-size: $f13; }
      .dropdown-toggle-split {
        min-width: 0;
        padding-left: 15px!important;
        padding-right: 15px!important;
        &:after {
          right: 10px;
          font-size: $f8; } } } } }


.hl_rules--wrap {
  margin-top: 40px;
  > .row {
    position: relative;
    > div {
      margin-bottom: 40px;
      @media (min-width: $lg-min) {
        &:first-child {
          padding-right: 30px; }
        &:last-child {
          padding-left: 30px; } } }
    .the-arrow {
      display: block;
      width: 100%;
      text-align: center;
      color: $text-drk;
      @media (min-width: $lg-min) {
        position: absolute;
        margin: auto;
        top: 100px;
        left: 0;
        right: 0;
        .icon {
          display: block;
          font-size: $f18!important;
          transform: rotate(-90deg); } }
      .icon {
        font-size: $f20; } } }
  h4 {
    font-size: $f18;
    margin-bottom: 20px;
    strong {
      font-size: $f22;
      display: inline-block;
      margin-right: 10px; } }
  .card {
    position: relative;
    .close {
      position: absolute;
      top: 15px;
      right: 15px;
      font-size: $f10; }
    .card-body {
      padding-top: 25px; }
    .form-group {
      label {
        font-size: $f14;
        color: $text-drk; }
      &.form-btn-group {
        display: flex;
        align-items: center;
        padding-top: 20px;
        > * {
          margin-right: 15px; } } }
    .form-input-dropdown {
      display: block;
      width: 100%;
      position: relative;
      .icon {
        position: absolute;
        top: 20px;
        right: 20px;
        font-size: $f12; }
      .dropdown-menu {
        min-width: 0;
        width: 100%;
        max-height: 300px;
        overflow-y: auto;
        .dropdown-item {
          white-space: normal;
          padding-top: 10px;
          padding-bottom: 10px;
          h5 {
            font-size: $f14;
            margin-bottom: 0; } } } }
    .form-input-group {
      display: flex;
      align-items: center;
      > * {
        flex: 1 0 0; } }
    .form-input-group-item {
      position: relative;
      .icon {
        position: absolute;
        top: 20px;
        right: 20px;
        font-size: $f12; }
      .form-control {
        padding-right: 40px; }
      .dropdown-menu {
        min-width: 0;
        width: 100%;
        max-height: 300px;
        overflow-y: auto;
        .dropdown-item {
          white-space: normal;
          padding-top: 10px;
          padding-bottom: 10px;
          h5 {
            font-size: $f14;
            margin-bottom: 0; } } }
      &:first-child {
        max-width: 150px;
        .form-control {
          border-bottom-right-radius: 0;
          border-top-right-radius: 0;
          border-right: 1px solid darken($border, 5%); } }
      &:last-child {
        .form-control {
          border-bottom-left-radius: 0;
          border-top-left-radius: 0; } } }
    .alert {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      p {
        margin-right: 20px; } }
    .card-footer {
      .change-actions {
        margin-bottom: 10px; }
      .form-group {
        margin-bottom: 0; } } }
  .and-or {
    font-weight: 500;
    margin-top: -10px;
    margin-bottom: 10px;
    margin-left: 20px;
    color: $text-drk;
    position: relative;
    &:before {
      content: "";
      display: block;
      width: 1px;
      height: 15px;
      background-color: #ccc;
      margin-left: 10px;
      margin-bottom: 5px; }
    &:after {
      content: "";
      display: block;
      width: 1px;
      height: 15px;
      background-color: #ccc;
      margin-left: 10px;
      margin-top: 5px; }
    .bootstrap-select {
      > .btn.dropdown-toggle {
        background: $white!important;
        padding: 7px 11px;
        padding-right: 20px!important;
        height: 34px;
        font-size: $f14;
        width: 65px;
        min-width: 0!important;
        &.bs-placeholder {
          .filter-option {
            font-weight: 500;
            color: $text-drk;
            text-transform: uppercase; } }
        .filter-option {
          font-weight: 500;
          color: $text-drk;
          top: 5px;
          text-transform: uppercase; }
        &:after {
          font-size: $f8;
          top: 53%;
          right: 10px;
          color: $text!important; } } } } }
