/* ////========== CUSTOM ICONS STYLES =========//// */

.icon {
  &.--blue {
    color: $blue; }
  &.--yellow {
    color: $yellow; }
  &.--green {
    color: $green; }
  &.--red {
    color: $red; }
  &.--orange {
    color: $orange; }
  &.--purple {
    color: $purple; }
  &.--teal {
    color: $teal; }
  &.--gray {
    color: $text; }
  &.--gray-drk {
    color: $text-drk; }
  &.--gray-lt {
    color: $text-lt; } }
