.rating {
  display: block;
  .icon {
    color: $yellow;
    font-size: $f18;
    display: inline-block;
    + .icon {
      margin-left: 4px; } }
  //Small
  &.--sm {
    .icon {
      font-size: $f12;
      + .icon {
        margin-left: 2px; }
      &.icon-star {
        color: #dfe3e4; } } } }
