/* ////========== WIDGETS STYLES =========//// */

.hl_widgets {
  //header
  &--header {
    border-bottom: 2px solid $hl-grey;
    margin-bottom: 35px;
    @media (min-width: $md-min) {
      margin-top: 80px; }
    &-inner {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 20px;
      h2 {
        font-size: $f20; }
      .btn {
        .icon {
          color: $green; } } } }
  //nav
  &--nav {
    list-style: none;
    margin: 0;
    padding: 0;
    @media (max-width: $sm-max) {
      padding-bottom: 10px; }
    @media (min-width: $md-min) {
      transform: translateY(2px); }
    li {
      @media (min-width: $md-min) {
        display: inline-block;
        &:not(last-child) {
          padding-right: 15px; } }
      @media (min-width: $lg-min) {
        &:not(last-child) {
          padding-right: 30px; } }
      a {
        font-size: $f14;
        color: $text;
        @media (min-width: $md-min) {
          display: block;
          padding-bottom: 10px;
          border-bottom: 3px solid transparent; }
        &:hover,
        &:focus,
        &:active {
          color: $blue; } }
      &.active {
        a {
          color: $blue;
          font-weight: 500;
          border-color: $blue; } } } } }

.hl_widgets--tools {
  margin-bottom: 40px;
  .form-group {
    label {
      font-size: $f12;
      margin-bottom: 5px; }
    .form-control {
      background: $white;
      min-width: 360px; } }
  .hl_controls--right {
    align-items: flex-end;
    transform: translateY(-5px);
    .form-group {
      display: block!important;
      label {
        font-size: $f12!important;
        margin-right: 0;
        margin-bottom: 5px; } } }
  .color-select {
    background: $white;
    padding: 10px 12px;
    height: 50px;
    p {
      margin-right: 10px;
      color: $text-drk; } }
  .btn-blue {
    height: 50px;
    //margin-top: 26px
    .icon {
      font-size: $f12;
      &:first-child {
        margin-right: 0!important; } } } }

.layout-select {
  display: flex;
  &-item {
    &:not(:last-child) {
      margin-right: 10px; }
    input {
      display: none;
      opacity: 0;
      visibility: hidden;
      &:checked {
        + label {
          color: $blue;
          background-color: rgba($hl-blue, 0.1); } } }
    label {
      width: 53px;
      height: 50px;
      margin-bottom: 0!important;
      background-color: $white;
      border-radius: 4px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: $f14!important;
      font-weight: bold;
      color: $hl-text-lt;
      cursor: pointer;
      transition: $hover;
      &:hover {
        color: $blue; } } } }


.hl_widget--header {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  margin-bottom: 10px; }

.hl_widget--tabs {
  margin-bottom: 40px;
  .tab-pane {
    padding-top: 0; }
  iframe {
    display: block;
    margin: 0 auto;
    border: none;
    max-width: 1160px;
    transition: all .3s ease-in-out; } }

//View Select
.hl_view-select {
  &.nav {
    display: flex;
    align-items: center;
    > a {
      color: #afb8bc;
      &:not(:last-child) {
        margin-right: 25px;
        position: relative;
        &:before {
          content: "";
          display: block;
          width: 3px;
          height: 3px;
          background-color: #afb8bc;
          border-radius: 50%;
          position: absolute;
          top: 40%;
          right: -14px; } }
      &.active {
        color: $hl-blue; } } } }


.hl_screen-select {
  display: flex;
  align-items: center;
  &-item {
    &:not(:last-child) {
      margin-right: 10px; }
    input {
      display: none;
      opacity: 0;
      visibility: hidden;
      &:checked {
        + label {
          color: #188bf6;
          background-color: rgba(#188bf6, 0.1);
          svg {
            fill: #188bf6; } } } }
    label {
      width: 43px;
      height: 43px;
      line-height: 43px;
      text-align: center;
      margin-bottom: 0;
      background-color: #ffffff;
      border-radius: 4px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 14px;
      font-weight: bold;
      color: #607179;
      cursor: pointer;
      transition: all .2s ease-in-out;
      &:hover {
        color: #188bf6;
        svg {
          fill: #188bf6; } }
      svg {
        transition: all .2s ease-in-out;
        fill: rgba(#607179, 0.5); } } } }
