/* ////========== PAGE CREATOR STYLES =========//// */

.hl_wrapper {
  &.d-flex {
    align-items: stretch; }
  &.nav-shrink {
    .hl_wrapper--inner {
      width: 100%;
      &.page-creator {
        padding-top: 0;
        @media (min-width:$md-min) {
          padding-top: 75px; }
        @media (min-width: 1200px) and (max-width: 1238px) {
          padding-top: 130px; }
        .hl_page-creator--main {
          width: 100%;
          @media (min-width:$xl-min) {
            left: 70px; } } } } } }


.page-creator {
  position: relative;
  &.--menu-active {
    &:before {
      content: "";
      display: block;
      background: rgba(#000, 0.2);
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 3;
      transition: $hover; } } }


.hl_page-creator--content {
  width: 100%;
  min-width: 100%;
  background-color: #fff;
  transition: $hover;
  border: 1px solid $border;
  &.--mobile {
    max-width: 400px;
    min-width: 0;
    margin: 0 auto;
    .hl_page-creator--column {
      width: 100%;
      min-width: 100%;
      flex: 1 0 0; } } }

.hl_page-creator--menu {
  background-color: darken(#E7F3FE, 2%);
  padding-top: 15px;
  padding-left: 15px;
  padding-right: 15px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  @media (min-width:$md-min) {
    position: fixed;
    margin: auto;
    top: 85px;
    left: 70px;
    right: 0;
    z-index: 2; }
  .menu--left {
    > * {
      margin-right: 10px;
      margin-bottom: 15px; } }
  .menu--right {
    > * {
      margin-bottom: 15px;
      &:not(:last-child) {
        margin-right: 10px; } } }
  .dropdown {
    .dropdown-toggle {
      border-radius: 0;
      padding: 9.5px 15px!important;
      margin-left: -1px;
      margin-right: -1px;
      &:after {
        display: none; } }
    .dropdown-menu {
      .dropdown-item {
        &.active {
          background: none; } } } }
  .btn-group {
    > * {
      &:not(:last-child) {
        border-right: 1px solid darken($border, 5%);
        margin-right: 1px!important; }
      &:first-child {
        &.dropdown {
          .dropdown-toggle {
            border-top-left-radius: 4px;
            border-bottom-left-radius: 4px; } } }
      &:last-child {
        &.dropdown {
          .dropdown-toggle {
            border-top-right-radius: 4px;
            border-bottom-right-radius: 4px; } } } }
    .btn {
      min-width: 45px;
      .btn-text {
        display: none;
        margin-left: 10px;
        font-weight: 500;
        @media (min-width:$xl-min) {
          display: inline-block; } } } } }


.hl_page-creator--settings-group {
  width: 100%;
  max-width: 400px;
  background-color: #fff;
  border-top: 1px solid $border;
  position: fixed;
  top: 84px;
  right: 0;
  bottom: 0;
  z-index: 4;
  transition: $hover;
  transform: translateX(100%);
  @media (max-width: $sm-max) {
    top: 123px; }
  &.active {
    transform: translateX(0);
    box-shadow: 5px 10px 30px 0 rgba(0,0,0,0.15);
    .close-group {
      opacity: 1; } }
  .close-group {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 35px;
    height: 35px;
    background-color: #E7F3FE;
    color: $text-drk;
    position: absolute;
    top: 0;
    left: -35px;
    opacity: 0;
    &:hover {
      color: $blue; } }
  .tab-content {
    .tab-pane {
      padding: 0;
      position: relative;
      > div {
        padding: 0 20px 20px 20px;
        height: calc(100vh - 150px);
        overflow-y: auto; } } } }

.hl_settings-group {
  h2 {
    font-size: $f16;
    background-color: #E7F3FE;
    padding: 15px 20px;
    margin-bottom: 20px;
    text-align: center; }
  hr {
    width: auto;
    margin-left: -20px;
    margin-right: -20px; }
  .integrations {
    .text-center {
      max-width: 300px;
      margin: 0 auto;
      h5 {
        font-size: $f14; }
      p {
        font-size: $f12; } }
    .not-connected {
      color: rgba($red, 0.9);
      text-transform: uppercase;
      font-size: $f12;
      font-weight: bold;
      padding: 7px 15px;
      border-radius: 4px;
      border: 2px solid $border;
      max-width: 150px;
      margin: 20px auto;
      .icon {
        display: inline-block;
        margin-right: 5px;
        font-size: $f10; } } }
  .seo {
    margin-top: -20px;
    .warning {
      text-align: center;
      color: $yellow;
      background-color: rgba($yellow, 0.2);
      padding: 10px 20px;
      margin-left: -20px;
      margin-right: -20px;
      margin-bottom: 20px; }
    h3 {
      color: $text;
      text-align: center;
      font-size: $f14;
      text-transform: uppercase;
      margin-bottom: 20px; }
    .seo-card {
      border: 2px solid $border;
      border-radius: 4px;
      padding: 15px;
      margin-bottom: 10px;
      p {
        + p {
          margin-top: 0; } }
      &.--seo1 {
        .seo-title {
          color: #1a0dab;
          font-size: $f18;
          font-weight: normal;
          margin-bottom: 5px; }
        .seo-link {
          color: #006621;
          font-size: $f14; }
        .seo-description {
          color: #545454;
          font-size: $f13; } }
      &.--seo2 {
        .seo-title {
          font-family: Georgia, 'lucida grande', tahoma, verdana, arial, sans-serif;
          color: #333;
          font-size: $f18;
          font-weight: normal;
          margin-bottom: 5px; }
        .seo-link {
          color: #9DA3AD;
          font-size: $f11;
          text-transform: uppercase; }
        .seo-description {
          color: #545454;
          font-size: $f13; } } } } }


