.hl_page-creator--section {
  padding: 30px;
  border: 2px solid transparent;
  position: relative;
  transition: $hover;
  > .hl_page-creator--actions {
    opacity: 0;
    transition: $hover;
    .move-actions {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 2;
      span {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 25px;
        height: 25px;
        background-color: $green;
        color: $white;
        transition: $hover;
        font-size: $f12;
        cursor: pointer;
        &:hover,
        &:focus,
        &:active {
          background-color: darken($green, 5%); } } }
    .more-actions {
      position: absolute;
      top: 0;
      right: 0;
      z-index: 2;
      span {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 26px;
        height: 26px;
        font-size: $f12;
        background-color: $green;
        color: $white;
        transition: $hover;
        cursor: pointer;
        &:hover,
        &:focus,
        &:active {
          background-color: darken($green, 5%); } } } }
  > .add-new-section {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: $green;
    color: $white;
    font-size: 12px;
    cursor: pointer;
    transition: $hover;
    position: absolute;
    margin: auto;
    left: 0;
    right: 0;
    bottom: -15px;
    z-index: 2;
    opacity: 0;
    &:hover {
      background: darken($green, 5%); } }
  > .new-row-blank {
    display: block;
    max-width: 1200px;
    margin: 0 auto;
    border-radius: 4px;
    background: rgba($blue, 0.1);
    border: 1px solid rgba($blue, 0.2);
    text-align: center;
    padding: 7px;
    transition: $hover;
    cursor: pointer;

    &:hover {
      background: rgba($blue, 0.2);
      .btn {
        background: $hl-blue;
        color: $white; } } }
  &.active {
    border-color: $green;
    > .hl_page-creator--actions {
      opacity: 1; }
    > .add-new-section {
      opacity: 1; } } }

//Row
.hl_page-creator--row {
  @include make-row();
  max-width: 1200px;
  margin: 0 auto;
  transition: $hover;
  position: relative;
  > .hl_page-creator--actions {
    opacity: 0;
    transition: $hover;
    .move-actions {
      display: flex;
      position: absolute;
      top: -25px;
      left: 0;
      z-index: 2;
      span {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 25px;
        height: 25px;
        background-color: $blue;
        color: $white;
        transition: $hover;
        font-size: $f12;
        cursor: pointer;
        &:hover,
        &:focus,
        &:active {
          background-color: darken($blue, 5%); } } }
    > .more-actions {
      display: flex;
      position: absolute;
      top: -25px;
      right: -2px;
      z-index: 2;
      span {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 25px;
        height: 25px;
        font-size: $f12;
        background-color: $blue;
        color: $white;
        transition: $hover;
        cursor: pointer;
        &:hover,
        &:focus,
        &:active {
          background-color: darken($blue, 5%); } } } }
  > .add-new-row {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: $blue;
    color: $white;
    font-size: 12px;
    cursor: pointer;
    transition: $hover;
    position: absolute;
    margin: auto;
    left: 0;
    right: 0;
    bottom: -12px;
    z-index: 2;
    opacity: 0;
    &:hover {
      background: darken($blue, 5%); } }
  .new-element-blank {
    display: block;
    max-width: 1200px;
    margin: 0 auto;
    border-radius: 4px;
    background: rgba($orange, 0.1);
    border: 1px solid rgba($orange, 0.2);
    text-align: center;
    padding: 7px;
    transition: $hover;
    cursor: pointer;
    &:hover {
      background: rgba($orange, 0.2);
      .btn {
        background: $hl-orange;
        color: $white; } } }
  &.active {
    > .hl_page-creator--actions {
      opacity: 1; }
    > .hl_page-creator--column {
      border-color: $blue; }
    > .add-new-row {
      opacity: 1; } } }

//Column
.hl_page-creator--column {
  @include make-col-ready();
  padding: 25px;
  border: 2px solid transparent;
  transform: translateX(2px);
  transition: $hover;
  flex: 1 0 0;
  &:not(first-child) {
    margin-left: -2px; }
  @media (max-width:$sm-max) {
    width: 100%;
    min-width: 100%; }
  @media (min-width:$xl-min) {
    flex: 1 0 0;
    width: auto; } }

//Element
.hl_page-creator--element {
  border: 2px solid transparent;
  position: relative;
  transition: $hover;
  > .hl_page-creator--actions {
    opacity: 0;
    transition: $hover;
    .more-actions {
      display: flex;
      position: absolute;
      top: -25px;
      right: -2px;
      z-index: 2;
      span {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 25px;
        height: 25px;
        font-size: $f12;
        background-color: $orange;
        color: $white;
        transition: $hover;
        cursor: pointer;
        &:hover,
        &:focus,
        &:active {
          background-color: darken($orange, 5%); } } } }
  > .add-new-element {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: $orange;
    color: $white;
    font-size: 12px;
    cursor: pointer;
    transition: $hover;
    position: absolute;
    margin: auto;
    left: 0;
    right: 0;
    bottom: -12px;
    z-index: 2;
    opacity: 0;
    &:hover {
      background: darken($orange, 5%); } }
  &.active {
    border-color: $orange;
    > .hl_page-creator--actions {
      opacity: 1; }
    > .add-new-element {
      opacity: 1; } } }
