/* ////========== CARD INFOS STYLES =========//// */

.hl_cards--wrap {
  padding: 60px 15px;
  min-height: 100vh;
  background-color: #f2f7fa;
  @media (min-width: 768px) {
    padding-top: 0; }
  .card {
    max-width: 620px;
    margin-left: auto;
    margin-right: auto;
    .card-header {
      padding: 45px 30px;
      .rating {
        .icon {
          font-size: 14px; } }
      .rating-wrap {
        color: #2a3135;
        .rating {
          display: inline-block;
          margin-left: 5px; } } }
    .card-body {
      padding: 60px 30px;
      text-align: center;
      > * {
        max-width: 420px;
        margin-left: auto;
        margin-right: auto; }
      h1 {
        font-size: 32px;
        font-weight: 300;
        line-height: 1.5;
        margin-bottom: 30px; }
      p {
        font-size: 18px;
        line-height: 2; }
      .yelp {
        margin-top: 40px;
        p {
          font-size: 16px;
          line-height: 1.7; } }
      .direct-contact {
        margin-top: 40px;
        p {
          font-size: 16px;
          line-height: 1.7; }
        .btn {
          margin-top: 10px; } }
      .btn {
        margin-top: 35px;
        padding: 17px 20px;
        font-size: 18px;
        font-weight: 500;
        max-width: 400px;
        margin-left: auto;
        margin-right: auto;
        position: relative;
        @media (max-width: 400px) {
          font-size: 14px;
          padding: 17px 20px 17px 50px; }
        img {
          max-width: 33px;
          height: auto;
          border-radius: 50%;
          box-shadow: 5px 8.7px 20px 0 rgba(0, 0, 0, 0.1);
          position: absolute;
          top: 50%;
          left: 20px;
          transform: translateY(-50%);
          @media (max-width: 400px) {
            left: 10px; } }
        &.btn-yelp {
          background-color: #fff;
          border: 1px solid #C42026;
          color: #C42026;
          margin-top: 10px;
          &:hover,
          &:focus,
          &:active {
            background-color: #C42026;
            color: #fff; } } }
      .form-group {
        max-width: 400px;
        margin-left: auto;
        margin-right: auto;
        .btn {
          margin-top: 20px; } } }
    .card-footer {
      padding: 45px 30px;
      text-align: center;
      p {
        font-size: 14px;
        + p {
          margin-top: 2px; } } } }
  .powered-by {
    margin-top: 40px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    h4 {
      font-size: 14px;
      color: #607179;
      margin-top: 10px;
      a {
        color: #607179;
        text-decoration: underline; } } } }
